<template>
    <div class="engineer_root">
        <div class="box">
            <div class="left">
            <!-- <img src="../assets/images/left.png" > -->
            </div>
            <div class="right">
                <h4>{{title}}</h4>
                <form method="post" id="form">
                    <input class="acc" type="text" placeholder="工号" id="workNumber" v-model.lazy="workNumber" v-on:blur="checkWorkNumber">
                    <input class="acc" type="password" placeholder="密码" id="engineerPassword" v-model.lazy="workPassword">
                    <input class="acc" type="password" v-bind:placeholder="in3" id="invitationCode" v-model.lazy="workCode">
                    <input class="acc" type="password" placeholder="电话号码" v-model.lazy="telephone" v-show="title==='修改密码'">
                    <button value="Login" id = "login" class="submit" type="button" v-on:click="loginOrChangePassword">{{btn}}</button>
                </form>
            </div>   
        </div>
    </div>
</template>

<script>
    import axios from 'axios';

    const EngineerAccountComponent = {
        name : 'EngineerAccountComponent',
        props:{
            title:{
                type:String,
                required:true
            },
            in3:{
                type:String,
                required:true
            },
            btn:{
                type:String,
                required:true
            }
        },
        data() {
            return {
                workNumber:'',
                workPassword:'',
                workCode:'',
                telephone:'',
                checkNumber:''
            }
        },
        methods: {
            //检测工号是否存在
            checkWorkNumber(){
                if(this.title === '修改密码'){
                    //检测工号是否存在
                    axios({
                        method:"get",
                        url:"http://index.nat100.top/engineer/checkWorkNumberServlet?workNumber="+this.workNumber
                    }).then(resp=>{
                        console.log(resp.data);
                        if(resp.data === 'no_exist'){
                            this.checkNumber = '工号不存在'
                            this.$notify({
                                title: '错误',
                                message: '工号不存在',
                                type: 'error'
                            })
                        }
                    })
                }
            },
            //登录或修改密码
            loginOrChangePassword(){
                //登录
                if(this.btn === 'Login'){
                    if(this.workCode !== '!@superMan#'){
                        this.$notify({
                            title: '错误',
                            message: '邀请码错误',
                            type: 'error'
                        })
                    }
                    else{
                        let formData = {
                            workNumber : this.workNumber,
                            password: this.workPassword
                        }
                        axios({
                            method:'post',
                            url:'http://index.nat100.top/engineer/Engineer_loginServlet',
                            data:JSON.stringify(formData)
                        }).then((resp)=>{
                            if(resp.data === "success"){
                                this.$router.push({
                                    name : 'engineerChange'
                                })
                            }
                            else{
                                this.$notify({
                                    title: '错误',
                                    message: '账号或密码错误',
                                    type: 'error'
                                })
                            }
                        })
                    }
                }
                //修改密码
                else{
                    if(this.checkNumber !== '工号不存在'){
                        if(this.workNumber === '' || this.workPassword === '' || this.workCode === '' || this.telephone === ''){
                            this.$notify({
                                title: '错误',
                                message: '请完善账号信息',
                                type: 'error'
                            })
                        }
                        else{
                            if(this.workPassword !== this.workCode){
                                this.$notify({
                                    title: '错误',
                                    message: '两次密码不一致',
                                    type: 'error'
                                })
                            }
                            else{
                                let formData = {
                                    workNumber : this.workNumber,
                                    newPassword : this.workPassword,
                                    telephone : this.telephone
                                }
                                axios({
                                    method: "post",
                                    url: "http://index.nat100.top/engineer/changeEngineerServlet",
                                    data:JSON.stringify(formData)
                                }).then(resp=>{
                                    if(resp.data === 'success'){
                                        this.$notify({
                                            title: '成功',
                                            message: '密码修改成功',
                                            type: 'success'
                                        })
                                    }
                                    else{
                                        this.$notify({
                                            title: '错误',
                                            message: '电话号码错误',
                                            type: 'error'
                                        })
                                    }
                                })
                            }
                        }
                    }
                    else{
                        this.$notify({
                            title: '错误',
                            message: '工号错误',
                            type: 'error'
                        })
                    }
                }
            }
        }
    }
    export default EngineerAccountComponent
</script>

