        z-index: -1;
        background: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%) no-repeat;
    }

    ::selection {
        color: #fff;
        background-color: rgb(144,129,241);

    }
    .have{
        position: relative;
        top: 90px;
        left: 150px;
    }
    .box {
        position: relative;
        top: 20px;
        display: flex;
        overflow: hidden;
