<template>
    <section>
        <div class="container">
            <div class="user signinBX">
                <div class="imgBx">
                    <img src="../assets/images/注册.png" alt="" style="width:381px ; height: 502px;">
                </div>
                <div class="formBx">
                    <div id="form">
                        <h2>{{ title }}</h2>
                        <input type="text" placeholder="Username" id = "username" v-model.lazy="username" v-on:blur="checkUsername">
                        <input type="password" placeholder="Password" id = "password" v-model.lazy="password">
                        <input type="password" placeholder="ConfirmPassword" id = "ConfirmPassword" v-model.lazy="confirmPassword" v-show="btnName === 'Register'">
                        <input type="text"  placeholder="Check" id = "code" style="width: 100px" v-model.lazy="code">
                        <img :src="url" id = "checkCode" v-on:click="changeCode" ref="code">
                        <a href="javascript:;" id = "changeImage"  v-on:click="changeCode">看不清,换一张</a><br>
                        <button value="Login" id = "login" class="button_login" v-on:click="userLogin">{{btnName}}</button>
                        <p class="signup">don't have an account?<a href="javascript:;" id="Sign" v-on:click="userRegister">{{titleChange}}</a></p>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
    import axios from 'axios';
    const UserAccountForm = {
        name : 'UserAccountForm',
        props:['title','titleChange','btnName'],
        data(){
            return {
                username:'',
                password:'',
                confirmPassword:'',
                code:'',
                url:'http://index.nat100.top/user/checkCodeServlet',
                isRegister:false,//isRegister用来判断用户能否成功注册
            }
        },
        methods: {
            //更换验证码
           changeCode(){
                this.url='http://index.nat100.top/user/checkCodeServlet?'+new Date().getMilliseconds();
            },
            //登录或注册按钮的点击事件
            userLogin(){
                //点击登录按钮,提交用户输入的信息,做出判断
                if(this.btnName === 'Login'){
                        let formData = {
                            username : this.username,
                            password : this.password,
                            code : this.code
                        }
                        axios({
                            method:'post',
                            url:'http://index.nat100.top/user/loginServlet', 
                            data:JSON.stringify(formData)
                        }).then((resp)=>{
                            if(resp.data === 'success'){
                                localStorage.setItem('username',this.username)
                                this.$router.push({
                                    name:'index'
                                })
                            }
                            else{
                                this.$notify({
                                    title: '错误',
                                    message: resp.data,
                                    type: 'error'
                                })
                            }
                        })
                }
                else{ //注册
                    if(this.username === '' || this.password === '' || this.confirmPassword === '' || this.code === ''){
                        this.$message({
                            message : '请完善注册信息',
                            type : 'warning'
                        })
                    }
                    else{
                        if(this.isRegister){//如果用户名不存在,则允许注册
                            if(this.password !== this.confirmPassword){
                                this.$message({
                                    message : '两次输入的密码不一致',
                                    type : 'error'
                                })
                                }
                            else{
                                //所有条件符合 发送请求注册账号
                                let formData = {
                                    username : this.username,
                                    password : this.password,
                                    code : this.code
                                }
                                axios({
                                    method: "post",
                                    url: "http://index.nat100.top/user/registerServlet",
                                    data:JSON.stringify(formData)
                                }).then(resp=>{
                                    if(resp.data === 'success'){
                                        //注册成功后,向用户信息表中添加账号名称
                                        axios({
                                            method:"get",
                                            url:"http://index.nat100.top/user/addUserInformation?account="+this.username
                                        }).then(()=>{
                                            this.$message({
                                                message:'注册成功',
                                                type : 'success'
                                            })
                                            //注册成功之后,切换到登录页面
                                            this.$router.push({
                                                name : 'userLogin'
                                            })
                                        })
                                    }
                                    else{
                                        this.$message({
                                            message : resp.data,
                                            type : 'error'
                                        })
                                    }
                                })
                            }
                        }
                        else{
                            this.$message({
                                message : '用户名已经存在,不允许注册',
                                type : 'error'
                            })
                        }
                    }
                }
            },
            //登录注册页面的切换
            userRegister(){
                if(this.btnName === 'Login'){
                    //切换到注册页面
                        this.$router.push({
                        name: 'userRegister'
                    })
                }
                else{
                    this.$router.push({
                        name : 'userLogin'
                    })
                }
            },
            //检测新用户注册时,用户名是否已经存在
            checkUsername(){
                if(this.btnName === 'Register'){
                    axios({
                        method:"get",
                        url:"http://index.nat100.top/user/registerServlet_username?username="+this.username,
                    }).then(resp=>{
                        if(resp.data === 'exist'){ //用户名已经存在,提示用户,并且禁止用户注册
                            this.$message({
                                message: '用户名已经存在,请更换',
                                type: 'warning'
                            })
                        }
                        else{
                            this.isRegister = true//允许用户注册
                        }
                    })
                }
            }
        }
    }
    export default UserAccountForm
</script>

<style scoped>
    * {
        font-family: 'Poppins', sans-serif;
    }

    #checkCode{
        margin-left: 20px;
        height: 38px;
    }

    .container .formBx .button_login,.button_register{
        background-color:cornflowerblue !important;
        width:70px;
        height:30px;
        text-align: center;
        color:#fff;
        line-height: 28px; border-radius:15px;
        border:0; outline: 0; transition: background 0.2s
    }

    .container .formBx .button_login:hover{
        background:#666
    }

    section {
        position: relative;
        min-height: 100vh;
        background: #557085;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 20px;
    }
    section.active{
        background:#3e3f39;
    }
    section .container {
        position: relative;
        width: 800px;
        height: 500px;
        background-color: #fff;
        box-shadow: 0 15px 50px rgba(0, 0, 0, 0.1);
    }

    section .container .user {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
    }

    section .container .user .imgBx {
        position: relative;
        width: 50%;
        height: 100%;
        transition: 0.5s;
    }

    section .container .user .imgBx img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    section .container .user .formBx {
        position: relative;
        width: 50%;
        height: 100%;
        background-color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 40px;
        transition: 0.5s;
    }

    section .container .user .formBx h2 {
        font-size: 18px;
        font-weight: 600;
        text-transform: uppercase;
        letter-spacing: 2px;
        text-align: center;
        width: 100%;
        margin-bottom: 10px;
        color: #555;
    }

    section .container .user .formBx input {
        width: 100%;
        padding: 10px;
        background-color: #f5f5f5;
        color: #333;
        box-shadow: none;
        font-size: 14px;
        margin: 8px 0;
        letter-spacing: 1px;
        font-weight: 300;
    }

    section .container .user .formBx input[type="submit"] {
        max-width: 100px;
        background-color: #677eff;
        color: #fff;
        cursor: pointer;
        font-size: 14px;
        font-weight: 500;
        letter-spacing: 1px;
        transition: 0.5s;
    }

    section .container .signupBx .formBx input[type="submit"] {
        background-color: #e73e49;
    }

    section .container .user .formBx .signup {
        position: relative;
        margin-top: 20px;
        font-size: 12px;
        letter-spacing: 1px;
        color: #555;
        text-transform: uppercase;
        font-weight: 300;
    }

    section .container .user .formBx .signup a {
        font-weight: 600;
        text-decoration: none;
        color: #577eff;
    }

    section .container .signupBx {
        pointer-events: none;
    }

    section .container.active .signupBx {
        pointer-events: initial;
    }

    section .container .signupBx .formBx {
        top: 100%;
    }

    section .container.active .signupBx .formBx {
        top: 0;
    }

    section .container .signupBx .imgBx {
        top: -100%;
        transition:0.5s;
    }

    section .container.active .signupBx .imgBx {
        top: 0;
    }

    section .container .signinBx .formBx{
        top:0;
    }

    section .container.active .signinBx .formBx{
        top:100%;
    }

    section .container .signinBx .imgBx{
        top:0;
        transition: 0.5s;
    }

    section .container.active .signinBx .imgBx{
        top:-100%;
    }

    @media (max-width:991px) {
        section .container{
            max-width: 400px;
        }

        section .container .imgBx{
            display: none;
        }

        section .container .user .formBx{
            width:100%;
        }
        section .container.active .signinBx .formBx{
            top:-100%;
        }
    }


</style>