<template>
    <li class="up up-time" v-on:click="buyBrand(brandInformation.id)">
        <a href="javascript:;" class="buyBrand">
            <img :src="require('../assets/images/'+brandInformation.imageUrl)" alt="图片加载失败" id="buyBrand_brandImg">
            <h3 id="buyBrand_brandName">{{ brandInformation.brandName }}</h3>
            <p id="buyBrand_companyName">{{ brandInformation.companyName }}</p>
            <p id="buyBrand_status" style='display: none'>{{ brandInformation.status }}</p>
            <span id="buyBrand_ordered">{{ brandInformation.ordered }}</span>
        </a>
    </li>
</template>

<script>
    
    const ArticleItem = {
        name : 'ArticleItem',
        props:{
            brandInformation:{
                type:Object,
                required:true
            }
        },
        mounted() {
            console.log(this.brandInformation);
        },
        methods: {
            buyBrand(id){
                //将商品对应的id存入会话
                sessionStorage.setItem('BrandId',id)
                this.$router.push({
                    name : 'buyBrand'
                })
            }
        }
    }
    export default ArticleItem
</script>

