import axios from "axios"
const Engineer = {
    namespaced:true,
    actions:{
        //查询工程师信息
        queryEngineerInformation(context){
            axios({
                method: "get",
                url: "http://index.nat100.top/engineer/selectAllEngineerInformation"
            }).then(resp=>{
                let formData = resp.data
                let engineerInformation ={
                    engineerName : formData.engineerName,
                    city : formData.city,
                    telephone : formData.telephone,
                    departName : '',//部门名称
                    onboarding : formData.onboarding
                }
                //通过改部门编号查询出工程师所属部门
                axios({
                    method: "get",
                    url: "http://index.nat100.top/engineer/engineerDepartmentServlet?department="+formData.department
                }).then(resp=>{
                    engineerInformation.departName = resp.data
                    context.commit('QUERYENGINEERINFORMATION',engineerInformation)
                })
            })
        }
    },
    mutations:{
        QUERYENGINEERINFORMATION(state,engineerInformation){
            state.engineerName = engineerInformation.engineerName
            state.city = engineerInformation.city
            state.telephone = engineerInformation.telephone
            state.departName = engineerInformation.departName
            state.onboarding = engineerInformation.onboarding
        }
    },
    state:{
        engineerName:'',//姓名
        city : '',//地址
        telephone:'',//电话号码
        departName : '',//部门名称
        onboarding : ''//入职时间
    }
}
export default Engineer