<template>
    <!-- 操作 -->
    <el-row>
        <el-button type="danger" plain v-on:click="deleteByIds">批量删除</el-button>
        <el-button type="primary" plain v-on:click="isGetmessage">新增</el-button>
    <!--    返回按钮开始禁用 当查询之后可以使用,重新渲染页面-->
        <el-button type="info" plain v-on:click="backBrand">返回</el-button>
    </el-row>
</template>

<script>
    import axios from 'axios'
    const EngineerFunction = {
        name : 'EngineerFunction',
        data() {
            return {
                selectIds:[]
            }
        },
        methods: {
            isGetmessage(){
                this.$bus.$emit('isGetmessage',true)
            },
            //批量删除
            deleteByIds(){
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    //用户点击确认按钮
                    //发送AJAX请求
                    //先查询商品名称
                    axios({
                        method:"post",
                        url:"http://index.nat100.top/brand/deleteByIds",
                        data:JSON.stringify(this.selectIds)
                    }).then((resp) => {
                        if(resp.data === "success"){
                            //删除成功
                            // 重新查询数据
                            this.$store.dispatch('BrandAbout/queryBrands')
                            // 弹出消息提示
                            this.$message({
                                message: '恭喜你，删除成功',
                                type: 'success'
                            })
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    })         
                })
            },
            //查询完成后返回 页面全部重新渲染
            backBrand(){
                this.$store.state.BrandAbout.brand={
                    status: '',
                    brandName: '',
                    companyName: '',
                    id: "",
                    ordered: "",
                    imageUrl:"无",
                    flag: "1"
                }
                this.$store.state.BrandAbout.pageSize = 5
                this.$store.state.BrandAbout.currentPage = 1
                this.$store.dispatch('BrandAbout/queryBrands')
            }
        },
        mounted() {
            //获取复选框被选中的全部商品的id
            this.$bus.$on('selectBrands',(multipleSelection)=>{
                for(let i = 0 ; i < multipleSelection.length ; i++){
                    this.selectIds[i] = multipleSelection[i].id
                }
            })
        },
        beforeDestroy() {
            this.$bus.$off(['selectBrands'])
        },
    }
    export default EngineerFunction
</script>