<template>
    <!--    收货地址-->
    <div class="address_root">
        <div class="receipt" v-show="isShow">
            <ul class="receipt_flex">
                <li>
                    <input type="text" placeholder="收件人姓名" name="consignee" id="consignee" v-model="addressee">
                    <input type="text" placeholder="收件地址" name="address" id="consignee_address" v-model="receiptAddress">
                    <input class="number" type="number" min="1" max="10" value="1" id="goods_number" v-model.number="brandNumber">
                </li>
            </ul>
            <span class="selected_address" v-show="isAddress">
                    <i>设置为默认收货地址: </i>
                    <input id="remember" name="remember" v-model="isRememeber" type="checkbox" value="1">
            </span>
            <div>
                <button id="submit_address" v-on:click="addBrand">{{titleNameBtn}}</button>
                <button id="close_address" v-on:click="close">关闭</button>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    import { mapState } from 'vuex'
    import { v4 as uuidv4 } from "uuid";//引入随机id生成器
    const BuyBrandAddress = {
        name : 'BuyBrandAddress',
        props:['isShow','isAddress','titleNameBtn'],
        data() {
            return {
                isRememeber:[],
                addressee:this.$cookies.get('addressee') === null ? '' : this.$cookies.get('addressee'),//收件人
                receiptAddress:this.$cookies.get('receiptAddress') === null ? '' : this.$cookies.get('receiptAddress'),//收件地址
                brandNumber : 1//商品数量
            }
        },
        computed:{
            ...mapState('BrandAbout',['queryBrand']),
            ...mapState('UserAbout',['username'])
        },
        methods: {
            close(){
                this.$bus.$emit('closeShow',false)
            },
            //购买商品,或将商品添加到购物车中
            addBrand(){
                if(this.titleNameBtn === '支付'){//直接购买商品
                    if(this.addressee === '' || this.receiptAddress === '' || this.brandNumber === ''){
                        this.$message({
                            message:'请完善收件信息',
                            type:'warning'
                        })
                    }
                    else{
                        let remember_value  = 0
                        if(this.isRememeber[0] === '1'){
                            remember_value = 1
                        }
                        else{
                            remember_value  = 0
                        }
                        let shoppingData = {
                            addressee:this.addressee,//收件人
                            brandName:this.queryBrand.brandName,//收件地址
                            brandImg: this.queryBrand.imageUrl,//商品图片
                            receiptAddress: this.receiptAddress,//收件地址
                            shoppingAccount: this.username,//登录的账号
                            price: this.queryBrand.ordered,//商品单价
                            brandNumber: this.brandNumber,//商品数量
                        }
                        axios({
                            method: "post",
                            url: "http://index.nat100.top/shopping/addBuyBrandServlet?remember=" + remember_value,
                            data: JSON.stringify(shoppingData)
                        }).then(()=>{
                            this.$message({
                                message:'购买成功',
                                type:'success'
                            })
                            this.$bus.$emit('closeShow',false)
                        })                        
                    }
                }
                else{//将商品添加到购物车中
                    if(this.addressee === '' || this.receiptAddress === '' || this.brandNumber === ''){
                        this.$message({
                            message:'请完善收件信息',
                            type:'warning'
                        })
                    }
                    else{
                        let buyCar_brandInformation = {
                            id:uuidv4(),
                            addressee: this.addressee,
                            brandName: this.queryBrand.brandName,
                            brandImg: this.queryBrand.imageUrl,
                            receiptAddress: this.receiptAddress,
                            price: parseInt(this.brandNumber) * parseInt(this.queryBrand.ordered),//所购买的商品的总价格
                            brandNumber: this.brandNumber,
                        }
                        let cookieData = this.$cookies.get('carBrandInformations')
                        // cookieData = cookieData.push(this.$cookies.get('carBrandInformations'))
                        if(cookieData === null){//判断用户是否是第一次使用
                            this.$cookies.set('carBrandInformations',[buyCar_brandInformation])
                        }
                        else{
                            let addFlag = 0;
                            let addIndex;
                            for(let i = 0 ; i < cookieData.length ; i++){
                                //如果新添加的商品 收件人 商品名称 收件地址重复 则不在重复添加 而是修改商品的数量 以及 商品价格
                                if(buyCar_brandInformation.addressee === cookieData[i].addressee && buyCar_brandInformation.brandName === cookieData[i].brandName && buyCar_brandInformation.receiptAddress === cookieData[i].receiptAddress){
                                    addFlag = 1 ;//1表示有重复商品
                                    addIndex = i;//记录有重复的数据的下标;
                                    break;
                                }
                                else{
                                    addFlag = 0;
                                }
                            }
                            //如果没有重复商品 正常添加
                            if(addFlag === 0){
                                cookieData.push(buyCar_brandInformation)
                                //先删除
                                this.$cookies.remove('carBrandInformations')
                                //添加
                                this.$cookies.set('carBrandInformations',cookieData)
                            }
                            else{
                                cookieData[addIndex].brandNumber = parseInt(cookieData[addIndex].brandNumber) + parseInt(buyCar_brandInformation.brandNumber)//新的商品数量
                                cookieData[addIndex].price = parseInt(cookieData[addIndex].price) + parseInt(buyCar_brandInformation.price)//新的商品价格
                                //如果存在重复商品,先将原有的cookie删除,在将修改后的cookieData添加为cookie
                                this.$cookies.remove('carBrandInformations')
                                this.$cookies.set('carBrandInformations',cookieData)
                            }
                        }
                        this.$bus.$emit('closeShow',false)//关闭地址填写框
                        this.$message({
                            message:'添加成功',
                            type:'success'
                        })
                    }
                }
            }
        }
    }
    export default BuyBrandAddress
</script>

<style scoped>
    #submit_address{
        margin-right: 20px;
    }

    .receipt{
        display: flex;
        flex-direction: column;
        border-radius: 2%;
        position: absolute;
        left: 320px;
        width: 400px;
        height: 400px;
        background-color: #fff;
        box-shadow: 0 4px 8px 0 rgba(16, 11, 11, 0.2), 0 6px 20px 0 rgba(210, 101, 11, 0.19);
    }

    .receipt .receipt_flex{
        display: flex;
        flex-direction: column;
    }

    .receipt .receipt_flex li input:nth-child(-n+2){
        border: 1px solid #c0c4cc;
        border-radius: 5px;
        width: 72%;
        padding: 10px 15px;
        box-sizing: border-box;
    }

    .receipt .receipt_flex li input:nth-child(-n+3):focus{
        border-color: #f07b00;
        outline: 0;
        -webkit-box-shadow: inset 0 1px 1px
        rgba(0, 0, 0, 0.075),
        #f07b00;
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
        #f07b00;
    }

    .receipt .receipt_flex li input{
        margin-left: 20px;
        margin-top: 10px;
        margin-bottom: 20px;
    }

    .receipt .receipt_flex li .number {
        border: 1px solid #c0c4cc;
        border-radius: 5px;
        width: 20%;
        padding: 10px 15px;
        box-sizing: border-box;
    }


    .receipt .selected_address{
        font-size: 14px;
        position: relative;
        top: 20px;
        margin-left: 20px;
    }

    .receipt button{
        position: relative;
        top: 70px;
        background: #cfccc9;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        padding: 0 20px;
        text-align: center;
        width: 100px;
        height: 50px;
    }
</style>