//引入vue
import Vue from "vue";

//引入vuex 
import Vuex from 'vuex'

//引入vuex模块
import shoppignCar from "./shoppingCar";
import Brand from "./Brand";
import Engineer from "./Engineer";
import User from "./User";
//使用vuex插件
Vue.use(Vuex)

const store = new Vuex.Store({
    modules:{
        ShoppingCarAbout:shoppignCar,
        BrandAbout : Brand,
        EngineerAbout : Engineer,
        UserAbout : User
    }
})

export default store
