<!-- 商品展示 -->
<template>
    <el-table
            :data="$store.state.BrandAbout.tableData"
            style="width: 100%"
            :row-class-name="tableRowClassName"
            @selection-change="handleSelectionChange">
        <el-table-column
                type="selection"
                width="55">
        </el-table-column>
        <el-table-column
                type="index"
                width="50">
        </el-table-column>
        <el-table-column
                prop="brandName"
                label="品牌名称"
                align="center"
        >
        </el-table-column>
        <el-table-column
                prop="companyName"
                label="企业名称"
                align="center"
        >
        </el-table-column>
        <el-table-column
                prop="ordered"
                align="center"
                label="价格">
        </el-table-column>
        <el-table-column
                prop="statusStr"
                align="center"
                label="当前状态">
        </el-table-column>
        <el-table-column
                align="center"
                label="操作">
                <!-- slot-scope为所在行的数据 -->
                <template slot-scope="scope">
                    <el-button type="primary" v-on:click="changeShow(scope.row)">修改</el-button>
                    <el-button type="danger" v-on:click="deleteById(scope.row)">删除</el-button>
                </template>
        </el-table-column>
    </el-table>
</template>

<script>
    import axios from 'axios';
    const EngineerBrandTable = {
        name : 'EngineerBrandTable',
        data(){
            return {
                multipleSelection:[],//复选框选中的商品的信息
            }
        },
        methods: {
            tableRowClassName({row, rowIndex}) {
                if (rowIndex === 1) {
                    return 'warning-row';
                } else if (rowIndex === 3) {
                    return 'success-row';
                }
                return '';
            },
            // 复选框选中后执行的方法
            handleSelectionChange(val) {
                this.multipleSelection = val;
                // console.log(this.multipleSelection)
                //触发获取复选框全部选中的商品的id
                this.$bus.$emit('selectBrands',this.multipleSelection)
            },
            //修改框弹出 并且回显数据
            changeShow(brand){
                this.$bus.$emit('isDialogVisible',true)
                //商品数据回显
                let showData = {
                    status: '',
                    brandName: '',
                    companyName: '',
                    id: "",
                    ordered: "",
                    imageUrl:"",
                    flag: "1"
                }
                //根据id查询商品图片路径
                axios({
                    method:"get",
                    url:"http://index.nat100.top/brand/selectImgUrlByIdServlet?id="+brand.id
                }).then((resp) =>{
                    showData.imageUrl = resp.data;
                    //根据id查询商品信息
                    axios({
                        method:"get",
                        url:"http://index.nat100.top/brand/ChangeById?id="+brand.id
                    }).then((resp) => {
                        showData.status = resp.data.status;
                        showData.brandName = resp.data.brandName;
                        showData.companyName = resp.data.companyName;
                        showData.id = resp.data.id;
                        showData.flag = resp.data.flag;
                        showData.ordered = resp.data.ordered;
                        //给brand赋值,使其数据能够回显
                        this.$store.dispatch('BrandAbout/brandInformation',showData)
                    })
                })
            },
            //根据id单个删除商品
            deleteById(brand){
                let id = brand.id;//获取所选信息的id
                let brandName;
                this.$confirm('此操作将删除该数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    //用户点击确认按钮 发送请求 删除数据 重新渲染页面
                    //根据id查询商品名称
                    axios({
                        method:"get",
                        url:"http://index.nat100.top/brand/selectBrandNameById?id="+id
                    }).then((resp) =>{
                        brandName = resp.data;
                        //根据商品名称 删除图片
                        axios({
                            method:"get",
                            url:"http://index.nat100.top/brand/deleteImageUrl?brandName="+brandName
                        }).then((resp)=>{
                                axios({
                                    method:"get",
                                    url:"http://index.nat100.top/brand/deleteById?id="+id
                                }).then((resp)=>{
                                    if(resp.data === "success"){
                                        // 重新查询数据
                                        this.$store.dispatch('BrandAbout/queryBrands')
                                        // 弹出消息提示
                                        this.$notify({
                                            title: '删除',
                                            message: '删除成功',
                                            type: 'success'
                                        })
                                    }
                                })

                        })
                    })
                }).catch(() => {
                    //用户点击取消按钮
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    })
                })
            },
        }
    }
    export default EngineerBrandTable
</script>