<!-- 新增商品的对话框 -->
<template>
    <!--添加数据对话框表单-->
    <el-dialog
               title="编辑品牌"
               :visible.sync="getmessage"
               width="30%"
       >
           <el-form ref="form" v-bind:model="brand" label-width="80px">
               <el-form-item label="品牌名称">
                   <el-input v-model="brand.brandName"></el-input>
               </el-form-item>

               <el-form-item label="企业名称">
                   <el-input v-model="brand.companyName"></el-input>
               </el-form-item>

               <el-form-item label="价格">
                   <el-input v-model="brand.ordered"></el-input>
               </el-form-item>

               <el-form-item label="图片路径">
                   <el-input type="textarea" v-model="brand.imageUrl"></el-input>
               </el-form-item>

               <el-form-item label="状态">
                   <el-switch v-model="brand.status"
                              active-value="1"
                              inactive-value="0"
                   ></el-switch>
               </el-form-item>


               <el-form-item>
                   <el-button type="primary" v-on:click="addBrand">提交</el-button>
                   <el-button @click="getmessage = false">取消</el-button>
               </el-form-item>
           </el-form>

    </el-dialog>
</template>

<script>
    import axios from 'axios';
    const EngineerAddBrand = {
        name : 'EngineerAddBrand',
        data() {
            return {
                 //新增的弹框
                 getmessage:false,
                 //品牌模型
                 brand: {
                    status: '',
                    brandName: '',
                    companyName: '',
                    id: "",
                    ordered: "",
                    imageUrl:"无",
                    flag: "1"
                }
            }
        },
        methods: {
            isGetmessage(value){
                this.getmessage = value
            },
            //新增商品
            addBrand() {
                if(this.brand.status === ""){
                    this.brand.status = "0"
                }
                if(this.brand.brandName === "" || this.brand.companyName === "" || this.brand.imageUrl === "" || this.brand.ordered === "" || this.brand.imageUrl === ""){
                    this.$message({
                        showClose: true,
                        message: '请完善商品信息',
                        type: 'error'
                    })
                }
                else{
                    // 发送ajax请求，添加数据
                    //先判断图片路径是否重复
                    axios({
                        method:"get",
                        url:"http://index.nat100.top/brand/selectImageUrl?imageUrl="+this.brand.imageUrl+"&id="+0
                    }).then((resp) =>{
                        if(resp.data === "exist"){
                            this.$message({
                                showClose: true,
                                message: '图片路径重复',
                                type: 'error'
                            });
                        }
                        else{
                            //判断商品是否重复
                            axios({
                                method:"get",
                                url:"http://index.nat100.top/brand/selectRepeat?brandName="+this.brand.brandName+"&companyName="+this.brand.companyName+"&id="+0
                            }).then((resp) => {
                                if (resp.data === 0) {
                                    this.$message({
                                        showClose: true,
                                        message: '该商品已被删除,请查看日志',
                                        type: 'error'
                                    });
                                    //关闭窗口
                                    this.getmessage = false;
                                }
                                else if (resp.data === 1) {
                                    this.$message({
                                        showClose: true,
                                        message: '请勿重复添加商品',
                                        type: 'error'
                                    });
                                    //关闭窗口
                                    this.getmessage = false;
                                }
                                else if(resp.data === "no_repeat"){
                                    //新增商品
                                    if(this.brand.status === ""){
                                        this.brand.status = "禁用"
                                    }
                                    // 发送ajax请求，添加数据
                                    // console.log('json_brand',JSON.stringify(brand));
                                    axios({
                                        method:"post",
                                        url:"http://index.nat100.top/brand/addBrandServlet",
                                        data:JSON.stringify(this.brand)
                                    }).then((resp) => {
                                        if(resp.data === "success"){
                                            this.$message({
                                                showClose: true,
                                                message: '添加成功',
                                                type: 'success'
                                            })
                                            this.$store.dispatch('BrandAbout/queryBrands')
                                            this.getmessage = false
                                            //清空输入框中的内容
                                            this.brand = {
                                                status: '',
                                                brandName: '',
                                                companyName: '',
                                                id: "",
                                                ordered: "",
                                                imageUrl:"无",
                                                flag: "1"
                                            }
                                        }
                                    }) 
                                }
                            })

                        }
                    })
                }
            }
        },
        mounted() {
            //绑定全局事件,接收getmessage
            this.$bus.$on('isGetmessage',this.isGetmessage)
        },
        beforeDestroy() {
            //解绑该组件的全局事件总线
            this.$bus.$off(['isGetmessage'])
        }
    }
    export default EngineerAddBrand
</script>