<template>
    <div class="brandCar" v-show="$store.state.ShoppingCarAbout.isShow">
        <ul class="brandCar_flex">
        <li>
            <input type="text" placeholder="收件人姓名" name="consignee" id="consignee_car" v-model.trim="newAddressee">
            <input type="text" placeholder="收件地址" name="address" id="consignee_addressCar" v-model.trim="newReceiptAddress">
            <input class="number" type="number" min="1" max="10" value="1" id="goods_numberCar" v-model.number="newBrandNumber">
        </li>
        </ul>
        <button id="submit_addressCar" v-on:click="rightChange">确认</button>
        <button id="close_addressCar" v-on:click="offChange">关闭</button>
    </div>
</template>

<script>
    const ShoppingCarChange = {
        name : 'ShoppingCarChange',
        props:['id','addressee','receiptAddress','brandNumber','brandName'],
        data() {
            return {
                newAddressee:'',
                newReceiptAddress:'',
                newBrandNumber:''
            }
        },
        watch:{
            addressee(value){
                this.newAddressee = this.addressee
            },
            receiptAddress(value){
                this.newReceiptAddress = this.receiptAddress
            },
            brandNumber(value){
                this.newBrandNumber = this.brandNumber
            }
        },
        methods: {
            offChange(){
                //将vuex中的isShow修改为false,使修改框关闭
                this.$store.dispatch('ShoppingCarAbout/isShowChange',false)
                //触发自定义事件 关闭遮罩
                this.$bus.$emit('isChangeHidebg',false)
            },
            //确认修改
            rightChange(){
                //获取存在cookie内的数据
                let cookieData = this.$cookies.get('carBrandInformations')
                //确定修改商品之前 需要先判断 修改之后是否会造成商品的重复
                let changeFlag = 1; //1表示商品没有重复的
                //当收件人addressee 订单地址receiptAddress 商品名称 brandName  商品的数量 相同时
                //需要判断修改的商品是否是它本身
                console.log(this.brandName);
                for(let k = 0 ; k < cookieData.length ; k++){
                    if (k !== this.id && cookieData[k].addressee === this.newAddressee && cookieData[k].receiptAddress === this.receiptAddress && cookieData[k].brandName === this.brandName){
                    this.$message({
                        message:'商品已经存在购物车内,请勿重复添加',
                        type:'warning'
                    })
                    return
                    }
                }
                for(let i = 0 ; i < cookieData.length ; i++){
                    if(cookieData[i].id === this.id){
                        if(this.newAddressee !== '' && this.newAddressee !== cookieData[i].addressee){
                            cookieData[i].addressee = this.newAddressee
                        }
                        if(this.newReceiptAddress !== '' && this.newReceiptAddress !== cookieData[i].receiptAddress){
                            cookieData[i].receiptAddress = this.newReceiptAddress
                        }
                        if(this.newBrandNumber !== '' && this.newBrandNumber !== cookieData[i].brandNumber){
                            //先求商品单价
                            let onePrice = parseInt(cookieData[i].price) / parseInt(cookieData[i].brandNumber)
                            cookieData[i].brandNumber = this.newBrandNumber
                            cookieData[i].price = onePrice * this.newBrandNumber
                        }
                        break
                    }
                }
                //删除原cookie
                this.$cookies.remove('carBrandInformations')
                //存入新的cookie
                this.$cookies.set('carBrandInformations',cookieData)
                this.offChange()
                //重新查询一次cookie
                this.$bus.$emit('changeCookie')
            }
        }
    }
    export default ShoppingCarChange
</script>

<style scoped>

    .brandCar{
        z-index: 9;
        border-radius: 2%;
        position: absolute;
        top: 10px;
        left: 345px;
        width: 400px;
        height: 400px;
        background-color: #fff;
        box-shadow: 0 4px 8px 0 rgba(16, 11, 11, 0.2), 0 6px 20px 0 rgba(210, 101, 11, 0.19);
    }

    .brandCar .brandCar_flex{
        display: flex;
        flex-direction: column;
    }

    .brandCar .brandCar_flex li input:nth-child(-n+2){
        border: 1px solid #c0c4cc;
        border-radius: 5px;
        width: 80%;
        height: 100%;
        padding: 10px 15px;
        box-sizing: border-box;
    }

    .brandCar .brandCar_flex li input:nth-child(-n+3):focus{
        border-color: #f07b00;
        outline: 0;
        -webkit-box-shadow: inset 0 1px 1px
        rgba(0, 0, 0, 0.075),
        #f07b00;
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
        #f07b00;
    }

    .brandCar .brandCar_flex li input{
        margin-left: 20px;
        margin-top: 10px;
        margin-bottom: 20px;
    }

    .brandCar .brandCar_flex li .number {
        border: 1px solid #c0c4cc;
        border-radius: 5px;
        width: 20%;
        height: 100%;
        padding: 10px 15px;
        box-sizing: border-box;
    }



    .brandCar button{
        position: relative;
        top: 70px;
        left: 10px;
        background: #cfccc9;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        padding: 0 20px;
        text-align: center;
        width: 100px;
        height: 50px;
    }
    
    #submit_addressCar{
        margin-right: 10px;
    }
</style>