<template>
    <div class="left_Bottom">
            <ul class="left_BottomFlex">
                <li class="btn left_userInformation" v-on:click="userShow" v-bind:class="{active : activeIndex == 0}">个人信息</li>
                <li class="btn Cancel_account" v-on:click="destoryUser" v-bind:class="{active : activeIndex == 1}">注销账号</li>
                <li class="help">帮助中心</li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
            </ul>
    </div>
</template>

<script>
    const UserInformationAsideBottom = {
        name : 'UserInformationAsideBottom',
        data() {
            return {
                activeIndex : window.sessionStorage.getItem('activeIndex') === '' ? 0 :  window.sessionStorage.getItem('activeIndex')
            }
        },
        watch:{
            activeIndex:{
                immediate:true,
                deep:false,
                handler(newVal){
                    window.sessionStorage.setItem('activeIndex',newVal)
                }
            }
        },
        methods: {
            userShow(){
                this.$router.replace({
                    name : 'userInformationShow'
                })
                this.activeIndex = 0
            },
            destoryUser(){
                this.$router.replace({
                    name : 'destoryAccout'
                })
                this.activeIndex = 1
            }
        },
    }
    export default UserInformationAsideBottom
</script>

<style scoped>
    .active{
        background-color: #fc5531
    }
    .left_Bottom {
        background-color: black;
        width: 200px;
    }
    .left_Bottom .left_BottomFlex {
        text-align: center;
        display: flex;
        flex-direction: column;
    }
    .left_Bottom .left_BottomFlex li {
        height: 60px;
        line-height: 60px;
        color: white;
        font-size: 14px;
    }
    .color_change {
        background-color: #fc5531 !important;
    }
    .left_Bottom .left_BottomFlex li:nth-child(-n+3):hover{
        cursor: pointer;
        background-color: #fc5531
    }
</style>