<template>
    <header>
        <div class="head">
            <div class="head_left">
                <ul class="head_leftFlex">
                    <img src="../assets/images/logo-mi2.png" alt="" class="logo">
                    <span class="logoName">{{ titleNameLogo }}</span>
                </ul>
            </div>
            <div class="head_right">
                <ul class="head_rightFlex">
                    <li><router-link to="/Index" style="color: white;">返回</router-link></li>
                    <li><a href="javascript:;" style="color: white" v-on:click="routerWhere">{{ titleNameHeader }}</a></li>
                    <li>
                        <button class=" quit quitBrand" v-on:click="userQuit">退出</button>
                    </li>
                </ul>
            </div>
        </div>
    </header>
</template>

<script>
    const BuyBrandHeader = {
        name : 'BuyBrandHeader',
        props:['titleNameLogo','titleNameHeader'],
        methods: {
            //根据titleNameHeader判断是需要路由到个人中心还是购物车
            routerWhere(){
                if(this.titleNameHeader === '个人中心'){
                    this.$router.push({
                        name : 'userInformationShow'
                    })
                }
                else{
                    this.$router.push({
                        name : 'shoppingCar'
                    })
                }
            },
            //用户退出登录
            userQuit(){
                location.href = 'http://index.nat100.top/'
            }
        }
    }
    export default BuyBrandHeader
</script>

<style scoped>
    header {
        width: 100vm;
        background-color: black;
    }

    header .head {
        display: flex;
    }

    header .head .head_left {
        margin-left: 20px;
        padding-top: 20px;
        padding-bottom: 20px;
        line-height: 40px;
    }

    header .head .head_left .head_leftFlex {
        display: flex;
    }

    header .head .head_left .head_leftFlex .logo {
        width: 40px;
        height: 40px;
        margin-right: 10px;
    }

    header .head .head_left .head_leftFlex .logoName {
        color: #ccc;
        font-size: 24px;
        font-weight: 400;
    }

    header .head .head_right{
        margin-left: 1045px;
        line-height: 80px;
    }

    header .head .head_right .head_rightFlex {
        display: flex;
    }

    header .head .head_right .head_rightFlex li{
        color: #ccc;
        padding-left: 10px;
        padding-right: 10px;
        font-size: 14px;
    }

    header .head .head_right .head_rightFlex li:hover{
        cursor: pointer;
    
    }

    header .head .head_right .head_rightFlex li .quit{
        font-size: 16px;
        background-color:rgba(0, 0, 0, 0);
        color: chocolate;
    }
</style>