<template>
    <header>
        <div class="header">
            <ul class="header_flex w">
                <!--工程师姓名-->
                <li class="engineerName">{{engineerName}}</li>
                <li class="greeting">{{greeting}}</li>
                <li class="date">{{time}}</li>
             
                <li class="system"><h1>工程师管理系统</h1></li>
                <li class="quit" v-on:click="quitAccout">退出登录</li>
            </ul>
        </div>
    </header>
</template>

<script>
    import axios from 'axios'
    const EngineerBrandHeader = {
        name : 'EngineerBrandHeader',
        data() {
            return {
                nowTime :'nowTime',//定时器名称,
                greeting : '',//问候
                time:'',//时间
                engineerName:''
            }
        },
        methods: {
            //退出登录
            quitAccout(){
                location.href="http://index.nat100.top/engineer/EngineerQuitServlet";
            }
        },
        mounted() {
            //开启定时器,显示当前时间 
            this.nowTime = setInterval(()=>{
            let date = new Date()
            let hour = date.getHours()
            let minutes = date.getMinutes()
            if(hour < 10) {
                hour = '0'+hour
            }
            if(minutes < 10){
                minutes = '0'+minutes
            }
            let seconds = date.getSeconds()
            if(seconds < 10){
                seconds = '0'+seconds
            }
            if(hour >= 8 && hour < 12)
            {
                this.greeting = "早上好"
            }
            else if(hour >= 12 && hour <= 18)
            {
                this.greeting = "下午好"
            }
            else if(hour > 18 && hour <= 22)
            {
                this.greeting = "晚上好"
            }
            else
            {
                this.greeting = "注意休息"
            }
            //获取当前年 月 日 星期
            let nowYear = date.getFullYear()
            let nowMoth = date.getMonth()
            if(nowMoth < 10)
            {
                nowMoth = '0'+nowMoth
            }
            let nowDate = date.getDate()
            if(nowDate < 10)
            {
                nowDate = '0'+nowDate
            }
            let nowDay = date.getDay()
            let arr = ['星期日','星期一','星期二','星期三','星期四','星期五','星期六']
            //拼接
            let x = nowYear+'-'+nowMoth+'-'+nowDate+'-'+arr[nowDay]
            this.time = x + ' '+ hour + ': '+minutes+' :'+seconds
            },1000)
            //发送请求,查看工程师姓名
            axios({
                method:'get',
                url:'http://index.nat100.top/engineer/Engineer_showEngineerServlet'
            }).then((resp)=>{
                this.engineerName = resp.data
            })
        },
        beforeDestroy() {
            //当组件被销毁时,清除定时器
            clearInterval(this.nowTime)
        }
    }
    export default EngineerBrandHeader
</script>

<style scoped>
    header .header {
        background-color: #409EFF;
        box-shadow: 5px 5px 10px gray;
        height: 90px;
    }
    header .header .header_flex {
        display: flex;
        justify-content: center;
    }
    header .header .header_flex li {
        padding-left: 10px;
        font-size: 14px;
        color: white;
        line-height: 90px;
    }
    header .header .header_flex .system {
        padding-right: 369px;
        padding-left: 236px;
    }
</style>