<!-- 商品修改 -->
<template>
    <div class="handoff show" id="brand_show">
        <EngineerSearch title="修改"></EngineerSearch>
        <EngineerFunction></EngineerFunction>
        <EngineerBrandTable></EngineerBrandTable>
        <EngineerPagination></EngineerPagination>
        <!-- 增加商品的弹出框 -->
        <EngineerAddBrand></EngineerAddBrand>
        <!-- 修改商品的弹出框 -->
        <ChangeBrandDialog></ChangeBrandDialog>
    </div>
</template>

<script>
    import EngineerSearch from '@/components/EngineerSearch.vue';
    import EngineerFunction from '@/components/EngineerFunction.vue';
    import EngineerBrandTable from '@/components/EngineerBrandTable.vue';
    import EngineerPagination from '@/components/EngineerPagination.vue';
    import EngineerAddBrand from '@/components/EngineerAddBrand.vue';
    import ChangeBrandDialog from '@/components/ChangeBrandDialog.vue';
    const EngineerBrandChange = {
        name : 'EngineerBrandChange',
        components:{
            EngineerSearch: EngineerSearch,
            EngineerFunction : EngineerFunction,
            EngineerBrandTable : EngineerBrandTable,
            EngineerPagination : EngineerPagination,
            EngineerAddBrand : EngineerAddBrand,
            ChangeBrandDialog : ChangeBrandDialog
        },
        mounted() {
            //挂载完毕,先清空brand
            this.$store.state.BrandAbout.brand = {
                status: '',
                brandName: '',
                companyName: '',
                id: "",
                ordered: "",
                imageUrl:"无",
                flag: "1"
            }
            //分页条件查询商品
            this.$store.dispatch('BrandAbout/queryBrands')
        }
    }
    export default EngineerBrandChange
</script>

