//引入vue
import Vue from "vue";

//引入vue-router
import VueRouter from "vue-router";

//引入pages中放置的路由组件
import HandOff from '../pages/HandOff.vue'
import UserLogin from '../pages/UserLogin.vue'
import UserRegister from '../pages/UserRegister'
import EngineerLogin from '../pages/EngineerLogin.vue'
import Index from '../pages/Index.vue'
import EngineerBrand from '../pages/EngineerBrand.vue'
import EngineerPassword from '../pages/EngineerPassword.vue'
import BuyBrand from '../pages/BuyBrand.vue'
import ShoppingBrand from '../pages/ShoppingBrand.vue'
import ShoppingCar from '../pages/ShoppingCar.vue'
import FinishCar from '../pages/FinishCar.vue'
import UserInformation from '../pages/UserInformation.vue'
import UserInformationShow from '../pages/UserInformationShow.vue'
import DestroyAccount from '../pages/DestroyAccount.vue'
import EngineerBrandChange from '../pages/EngineerBrandChange.vue'
import EngineerInformation from '../pages/EngineerInformation.vue'
import EngineerLog from '../pages/EngineerLog.vue'
import EngineerLookBrand from '../pages/EngineerLookBrand'
//使用vue-router
Vue.use(VueRouter)

//创建一个路由器
const router = new VueRouter({
    // mode切换路由工作模式,默认值是hash工作模式,路径中会带有/#,/#/后面路径包括#不会发送到服务器上   
    // history工作模式不会有/#
    routes:[
        //切换
        {
            path:'/',
            component:HandOff,
            
            meta:{
                title:'首页'
            }
            
        },
        //用户登录
        {
            name:'userLogin',
            path:'/UserLogin',
            component:UserLogin,
            meta:{
                title:'用户登录'
            }
        },
        //用户注册
        {
            name : 'userRegister',
            path:'/UserRegister',
            component : UserRegister,
            meta:{
                title:'用户注册'
            }
        },
        //工程师登录
        {
            name : 'engineerLogin',
            path : '/EngineerLogin',
            component:EngineerLogin,
            meta:{
                title:'工程师登录'
            }
        },
        //工程师修改密码
        {
            name : 'engineerPassword',
            path : '/EngineerPassword',
            component : EngineerPassword,
            meta:{
                title:'修改密码'
            }
        },
        //工程师管理页面
        {
            name : 'engineerBrand',
            path : '/EngineerBrand',
            component : EngineerBrand,
            meta:{
                title : '工程师管理系统'
            },
            children:[
                {
                    name : 'engineerChange',
                    path : '/EngineerBrand/EngineerBrandChange',
                    component : EngineerBrandChange,
                    meta:{
                        title:'工程师管理系统(修改商品)'
                    }
                },
                {
                    name : 'engineerInformation',
                    path : '/EngineerBrand/EngineerInformation',
                    component : EngineerInformation,
                    meta:{
                        title:'工程师管理系统(个人信息)'
                    }
                },
                {
                    name : 'engineerLog',
                    path : '/EngineerBrand/EngineerLog',
                    component : EngineerLog,
                    meta:{
                        title:'工程师管理系统(日志)'
                    }
                },
                {
                    name : 'engineerLookBrand',
                    path : '/EngineerBrand/EngineerLookBrand',
                    component : EngineerLookBrand,
                    meta:{
                        title:'工程师管理系统(查看商品)'
                    }
                },
            ]
        },
        //商城页面
        {
            name : 'index',
            path : '/Index',
            component : Index,
            meta:{
                title : '商城'
            },
            props:true//将该路由收到的所有params参数以props形式传递给Index组件
        },
        //购物页面
        {
            name : 'buyBrand',
            path : '/BuyBrand',
            component : BuyBrand,
            meta:{
                title:'购买商品'
            }
        },
        //购物车页面
        {
            name : 'shoppingBrand',
            path : '/ShoppingBrand',
            component : ShoppingBrand,
            //嵌套路由
            children:[
                {
                    name : 'shoppingCar',
                    path : 'ShoppingCar',
                    component : ShoppingCar,
                    meta:{
                        title : '购物车'
                    }
                },
                {
                    name : 'finishCar',
                    path : 'FinishCar',
                    component : FinishCar,
                    meta:{
                        title : '已经支付订单'
                    }
                }
            ]
        },
        //用户信息
        {
            name : 'userInformation',
            path : '/UserInformation',
            component : UserInformation,
            children:[
                {
                    name : 'userInformationShow',
                    path : '/UserInformation/UserInformationShow',
                    component: UserInformationShow,
                    meta:{
                        title:'用户信息'
                    }
                },
                {
                    name : 'destoryAccout',
                    path:'/UserInformation/DestroyAccount',
                    component : DestroyAccount,
                    meta:{
                        title:'注销账号'
                    }
                }
            ]
        }
    ]
})
import axios from "axios";
//防止点击同一路由多次报错
const routerPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return routerPush.call(this, location).catch((error) => error)
}

const routerReplace= VueRouter.prototype.replace
VueRouter.prototype.replace = function push(location) {
    return routerReplace.call(this, location).catch((error) => error)
}

//全局前置路由守卫
router.beforeEach(function(to,from,next){
    //检测是否处于登录状态(用户 和 工程师)
    if(to.path.includes('/EngineerBrand') || to.path.includes('/BuyBrand') || to.path.includes('/ShoppingBrand') || to.path.includes('/UserInformation')){
        if(to.path.includes('/EngineerBrand')){//判断工程师是否登录
            axios({
                method:"get",
                url:"http://index.nat100.top/engineer/isEngineerLogin"
            }).then(resp=>{
                if(resp.data !== ''){
                    next()//如果登录,放行
                }
            })
        }
        else{
            axios({
                method:"get",
                url:"http://index.nat100.top/user/indexshow"
            }).then(resp=>{
                if(resp.data !== ''){
                    next()//放行
                }
            })
        }
    }
    else{
        next();//放行
    }
})

//全局后置路由守卫
router.afterEach(function(to,from){
    document.title = to.meta.title
})



export default router