<template>
        <!-- 头部制作 -->
    <header class="clearfix" id="top">
            <div class="header w">
                <ul class="left">
                    <li v-for="(title,index) in titles" v-bind:key="index"><a href="javascript:;">{{ title }}</a></li>
                    <li class="download">
                        <a href="javascript:;">下载App</a>
                        <div class="App">
                            <div class="erweima">
                                <img src="../assets/images/download.png" alt="" style="width: 90px; height: 90px">
                            </div>
                        </div>
                    </li>
                </ul>
                <ul class="right">
                    <li id="login">
                        <router-link to="/UserLogin" v-if="$store.state.UserAbout.username===''">登录</router-link>
                    </li>
                    <li id="register">
                        <router-link to="/UserRegister" v-if="$store.state.UserAbout.username===''">注册</router-link>
                    </li>
               
                    <li 
                        class="avatar" 
                        id="index_avatar" 
                        v-if="$store.state.UserAbout.avatarPath != '' && $store.state.UserAbout.nickname != ''" 
                        v-on:click="userInformationShow"
                    >
                        <img :src="'/uploadImages/'+$store.state.UserAbout.avatarPath" alt="" style="width: 57px; border-radius: 35px;">
                    </li>
                    <li 
                        class="nickname" 
                        v-if="$store.state.UserAbout.nickname != '' && $store.state.UserAbout.nickname != ''" 
                        style="color: aliceblue;" 
                        v-on:click="userInformationShow"
                    >{{ $store.state.UserAbout.nickname }}</li>
                    <div class="dropdown" id="dropDown_Car">
                        <button class="dropbtn" id="dropBtn_Car" v-on:click="shoppingBrand">购物车 ({{shoppingCarLength}})</button>
                        <div class="dropdown-content" id="dropContent" v-show="shoppingCarLength !== 0">
                            <ul class="content_flex">
                                <li class="content_brand" 
                                    v-for="(carBrandInformation,index) in $cookies.get('carBrandInformations')"
                                    v-bind:key="carBrandInformation.id"
                                >
                                    {{carBrandInformation.brandName}} {{ carBrandInformation.addressee }} {{ carBrandInformation.receiptAddress }}
                                </li>
                            </ul>
                        </div>
                    </div>
                    <li 
                        v-if="$store.state.UserAbout.nickname=='' || $store.state.UserAbout.nickname == null" 
                        style="color: aliceblue;" 
                        class="tip" 
                        v-on:click="userInformationShow"
                    >
                    完善个人信息</li>
                </ul>
            </div>
    </header>
</template>

<script>
    const IndexHeader = {
        name : 'IndexHeader',
        data(){
            return {
                titles:['小米官网','小米商城','MIUI','loT','云服务','天星数科','有品','小爱开放平台','企业团购','资质证照','协议规则'],
            }
        },
        computed:{
            shoppingCarLength:{
                get(){
                    return $cookies.get('carBrandInformations') === null ? 0 : $cookies.get('carBrandInformations').length
                }
            }
        },
        methods:{
            //通过路由跳转到购物车页面
            shoppingBrand(){
                this.$router.push({
                    name : 'shoppingCar'
                })
            },
            userInformationShow(){
                this.$router.push({
                    name:'userInformationShow'
                })
            }
        },
        mounted() {
            this.$store.dispatch('UserAbout/queryUsername')
            this.$store.dispatch('UserAbout/querUserInformation')
        },
    }
    export default IndexHeader
</script>

<style scoped>
    .tip{
        position: absolute;
        right: 50px;
    }
    .tip:hover{
        cursor: pointer;
    }
    header {
        min-width: 100%;
        background-color: rgb(29, 29, 29);
        height: 60px;
    }
    /* 下载App */
    .header {
        position: relative;
        line-height: 60px;
    }
    .header .left li {
        /* padding-left: 20px; */
        float: left;
    }

    .header .right li,
    button {
        float: right;
    }

    .header ul li a {
        color: rgb(107, 105, 105);
        font-size: 12px;
        padding-left: 12px;
        line-height: 40px;
    }

    .header ul li a:hover {
        color: #fff;
    }

    .header .right .dropdown .dropbtn {
        position: absolute;
        left: 955px;
        color: rgb(23, 23, 23);
        display: inline-block;
        padding: 4px 20px;
        line-height: 30px;
        background-color: rgba(29, 29, 29, 0);
    }

    .header .right .dropdown .dropbtn {
        background-color: #7d7d7d;
    }

    .header .right .dropdown .dropbtn:hover {
        background-color: rgb(255, 255, 255);
        color: rgb(242, 129, 8);
    }

    .header .right .dropbtn::before {
        padding-right: 2px;
        font-family: "icomoon";
        content: "\e93a";
    }
       /* 购物车下滑菜单 */
    /* 需要下拉的内容 */
    .dropdown {
        position: relative;
        top: 10px;
    }

    .header .right .dropdown .dropdown-content {
        display: none;
        position: absolute;
        /* 缓慢弹出的动画 */
        overflow: hidden;
        /* 绑定动画名字并且设置持续时间 */
        animation-name: hiddens-Jframes;
        animation-duration: 1s;
        top: 40px;
        left: 872px;
        z-index: 99;
        background-color: rgb(248, 248, 248);
        width: 25%;
        height: 100px;
        text-align: center;
        line-height: 100px;
        box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    }
    @keyframes hiddens-Jframes {
        from {
            height: 0px;
        }

        to {
            height: 100px;
        }
    }
    .header .right .dropdown:hover .dropdown-content {
        display: block;
    }
    

    .App {
        display: none;
        position: absolute;
        top: 23px;
        right: 556px;
        z-index: 99;
        width: 0px;
        height: 0px;
        text-align: center;
        border: 8px solid #fff;
        border-top-color: transparent;
        border-left-color: transparent;
        border-right-color: transparent;
    }

    .App .erweima img {
        position: absolute;
        top: 9px;
        right: -45px;
        /* 缓慢弹出的动画 */
        overflow: hidden;
        /* 绑定动画名字并且设置持续时间 */
        animation-name: Jframes;
        animation-duration: 1s;
    }


    .header .left .download:hover .App {
        display: block;
    }


    @keyframes Jframes {
        from {
            height: 0px;
        }

        to {
            height: 90px;
        }
    }
    /* 头像 */
    #index_avatar{
        position: absolute;
        right: 46px;
        width: 60px;
        height: 60px;
        border-radius: 50%;
    }

    #index_avatar,#index_nickname:hover{
        cursor: pointer;
    }

     
    .right .content_flex{
        display: flex;
        flex-direction: column;
    }

    .right .content_flex .content_brand{
        font-size: 14px;
        color: black;
        line-height: 25px;
    }
    
    #dropContent{
        overflow: hidden;
    }
    .nickname{
        font-size: 14px;
    }
    .nickname:hover{
        cursor: pointer;
    }
</style>