<template>
    <!--            修改电话号码的输入框-->
    <div class="change_telephone" v-show="telIsShow">
        <ul class="change_telephone_flex">
            <input type="number" placeholder="电话号码" class="in_telephone_one" v-model="telephone">
            <input type="number" placeholder="重新确认电话号码" class="in_telephone_two" v-model="confirmTelephone">
        </ul>
        <button class="telephone_submit" @click="changeTel">确认</button>
        <button class="telephone_close" v-on:click="closeTel">关闭</button>
    </div>
</template>

<script>
    import axios from 'axios'
    const EngineerChangeTelephone = {
        name : 'EngineerChangeTelephone',
        data() {
            return {
                telIsShow:false,
                telephone:'',
                confirmTelephone:''
            }
        },
        methods: {
            //关闭修改电话号码的弹出框
            closeTel(){
                this.telIsShow = false
            },
            //修改电话号码
            changeTel(){
                //输入不能为空
                if(this.telephone === '' || this.confirmTelephone === ' '){
                    this.$message({
                        type: 'error',
                        message: '电话号码不能为空'
                    })
                }
                //修改电话号码之前,先判断两次输入是否一致
                else if(this.telephone === this.confirmTelephone){
                    //如果两次输入一致,则发送请求,修改号码
                    axios({
                        method:'get',
                        url : 'http://index.nat100.top/engineer/updateTelephone?telephone='+this.telephone
                    }).then(resp=>{
                        if(resp.data === 'success'){
                            this.$message({
                                type: 'success',
                                message: '修改成功'
                            })
                            this.telIsShow = false//修改成功后关闭弹出框
                            //重新查询工程师信息
                            this.$store.dispatch('EngineerAbout/queryEngineerInformation')
                        }
                        else{
                            this.$message({
                                type: 'success',
                                message: '错误'
                            })
                        }
                    })
                }
                else{
                    this.$message({
                        type: 'error',
                        message: '两次号码输入不一致'
                    })
                }
            }
        },
        mounted() {
            this.$bus.$on('isCloseTel',(show)=>{
                this.telIsShow = show
            })
        }
    }
    export default EngineerChangeTelephone
</script>

<style scoped>
    .change_telephone {
        position: absolute;
        top: 55px;
        left: -45px;
        z-index: 2;
        width: 370px;
        height: 270px;
        background-color: #eef7f2;
    }
    .change_telephone .change_telephone_flex {
        display: flex;
        flex-direction: column;
        margin-left: 37px;
        margin-top: 45px;
    }
    .change_telephone .change_telephone_flex input:nth-child(-n+1) {
        margin-bottom: 25px;
    }
    .change_telephone .change_telephone_flex .in_telephone_two {
        margin-top: 20px;
    }
    .change_telephone input {
        outline-style: none;
        border: 1px solid #ccc;
        border-radius: 3px;
        padding: 13px 14px;
        width: 280px;
        font-size: 14px;
        font-weight: 700;
        font-family: "Microsoft soft";
        margin: 2px;
    }
    .change_telephone button {
        background-color: #1795bb;
        border-radius: 12px;
        border: none;
        color: white;
        padding: 16px 32px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        margin: 4px 43px;
        -webkit-transition-duration: 0.4s;
        transition-duration: 0.4s;
        cursor: pointer;
    }
</style>