<template>
    <div class="shoppingCarRoot">
        <el-table
            :data="tableData"
            style="width: 1230px"
            @selection-change="handleSelectionChange"
            max-height="675">
            <el-table-column
            type="selection"
            width="55">
            </el-table-column>
            <el-table-column
            fixed
            prop="brandName"
            label="商品名称"
            align='center'
            width="150">
            </el-table-column>
            <el-table-column
            prop="brandImg"
            label="商品"
            align='center'
            width="200">
                <template slot-scope="scope">
                    <el-image 
                        :src="require('../assets/images/'+scope.row.brandImg)" 
                        lazy
                        style="width: 100px; height: 100px"
                        ></el-image>
                </template>
            </el-table-column>
            <el-table-column
            prop="receiptAddress"
            label="收件地址"
            align='center'
            width="300">
            </el-table-column>
            <el-table-column
            prop="brandNumber"
            label="购买数量"
            align='center'
            width="120">
            </el-table-column>
            <el-table-column
            prop="price"
            label="价格"
            align='center'
            width="120">
            </el-table-column>
            <el-table-column
            prop="addressee"
            label="收件人"
            align='center'
            width="120">
            </el-table-column>
            <el-table-column
            fixed="right"
            label="操作"
            align='center'
            width="120">
                <template slot-scope="scope">
                    <el-button
                    @click.native.prevent="deleteRow(scope.$index, tableData)"
                    type="text"
                    size="small">
                    移除
                    </el-button>
                    <el-button
                    @click.native.prevent="changeBrand(scope.$index,tableData)"
                    type="text"
                    size="small">
                    修改
                    </el-button>
                    <el-button
                    @click.native.prevent="finishBrand(scope.$index,tableData)"
                    type="text"
                    size="small">
                    购买
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 修改框 -->
        <ShoppingCarChange 
            v-bind:id = "id"
            v-bind:addressee="addressee"
            v-bind:receiptAddress="receiptAddress"
            v-bind:brandNumber="brandNumber"
            v-bind:brandName = "brandName"
        ></ShoppingCarChange>
    </div>
</template>

<script>
    import axios from 'axios'
    import 'animate.css'//引入第三方动画库
    import ShoppingCarChange from './ShoppingCarChange.vue'
    const ShoppingCarTable = {
        name : 'ShoppingCarTable',
        components:{
            ShoppingCarChange : ShoppingCarChange
        },
        data() {
            return {
                tableData: [],
                id : '',//商品id
                addressee:'',//订单地址
                receiptAddress:'',//收件人
                brandNumber:'',//数量,
                brandName:''//商品名称
            }
        },
        methods: {
            //移除
            deleteRow(index, rows) {
                this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.id = rows[index].id
                    //点击购买按钮之后,发送请求,将商品添加到数据库中,并且删除cookie内存储的此商品
                    let cookieData = this.$cookies.get('carBrandInformations')
                    let newCookieData = []
                    for(let i = 0 ; i < cookieData.length ; i++){
                        if(cookieData[i].id !== this.id){
                            newCookieData.push(cookieData[i])
                        }
                    }
                    //删除原cookie
                    this.$cookies.remove('carBrandInformations')
                    //存入新的cookie
                    this.$cookies.set('carBrandInformations',newCookieData)
                    //重新查询一次cookie
                    this.$bus.$emit('changeCookie')
                    this.$message({
                        message:'删除成功',
                        type:'success'
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    })         
                })
            },
            //点击修改按钮,弹出修改框
            changeBrand(index,tableData){
                this.id = tableData[index].id
                //根据id查出购买信息,做数据回显
                let cookieData = this.$cookies.get('carBrandInformations')
                for(let i = 0 ; i < cookieData.length ; i++){
                    if(cookieData[i].id === this.id){
                        this.addressee = cookieData[i].addressee  
                        this.receiptAddress =  cookieData[i].receiptAddress 
                        this.brandNumber =  cookieData[i].brandNumber
                        this.brandName = cookieData[i].brandName
                        break
                    }
                }
                //将vuex中的isShow修改为true,使修改框弹出
                this.$store.dispatch('ShoppingCarAbout/isShowChange',true)
                //触发全局事件 弹出遮罩层
                this.$bus.$emit('isChangeHidebg',true)
            },
            //购买按钮
            finishBrand(index,rows){
                this.id = rows[index].id
                //点击购买按钮之后,发送请求,将商品添加到数据库中,并且删除cookie内存储的此商品
                let cookieData = this.$cookies.get('carBrandInformations')
                let newCookieData = []
                for(let i = 0 ; i < cookieData.length ; i++){
                    if(cookieData[i].id !== this.id){
                        newCookieData.push(cookieData[i])
                    }
                    else{//当找到需要购买的商品时,发送请求
                        let payBrand = {
                            addressee:cookieData[i].addressee,
                            brandName:cookieData[i].brandName,
                            brandImg:cookieData[i].brandImg,
                            receiptAddress:cookieData[i].receiptAddress,
                            shoppingAccount:localStorage.getItem('username'),
                            brandNumber:cookieData[i].brandNumber,
                            price:cookieData[i].price
                        }
                        axios({
                            method:"post",
                            url:"http://index.nat100.top/shopping/addBuyBrandServlet?remember=0",
                            data:JSON.stringify(payBrand)
                        }).then(()=>{
                            this.$message({
                                message:'购买成功',
                                type:'success'
                            })
                            // 删除原cookie
                            this.$cookies.remove('carBrandInformations')
                            //存入新的cookie
                            this.$cookies.set('carBrandInformations',newCookieData)
                            //重新查询一次cookie
                            this.$bus.$emit('changeCookie')
                        })
                    }
                }
            },
            //复选框被选中之后执行的方法
            handleSelectionChange(val) {
                this.$bus.$emit('selectShoppingCars',val)
            }
        },
        mounted() {
            this.tableData = this.$cookies.get('carBrandInformations')
            //重新查询一次cookie
            this.$bus.$on('changeCookie',()=>{
                this.tableData = this.$cookies.get('carBrandInformations')
            })
            //点击查询按钮之后,将查询到的数据更新给tableData
            this.$bus.$on('searchTableData',(value)=>{
                this.tableData = value
            })
        },
        beforeDestroy() {
            this.$bus.$off(['changeCookie','searchTableData'])
        }
    }
    export default ShoppingCarTable
</script>

<style>
    .shoppingCarRoot{
        position: absolute;
        top: 20px;
    }
</style>
