<template>
        <el-table
            :data="removeBrand"
            style="width: 100%"
            :row-class-name="tableRowClassName">
        <el-table-column
                type="index"
                width="50">
        </el-table-column>
        <el-table-column
                prop="brandName"
                label="品牌名称"
                align="center"
        >
        </el-table-column>
        <el-table-column
                prop="companyName"
                label="企业名称"
                align="center"
        >
        </el-table-column>
        <el-table-column
                prop="ordered"
                align="center"
                label="价格">
        </el-table-column>
        <el-table-column
                prop="statusStr"
                align="center"
                label="当前状态">
        </el-table-column>
        <el-table-column
                align="center"
                label="操作">
                <!-- slot-scope为所在行的数据 -->
                <template slot-scope="scope">
                    <el-button type="primary" v-on:click="restoreBrand(scope.row)">还原</el-button>
                </template>
        </el-table-column>
    </el-table>
</template>

<script>
    import axios from 'axios';
    const EngineerLogTable = {
        name : 'EngineerLogTable',
        computed:{
            removeBrand:{
                get(){
                    return this.$store.state.BrandAbout.removeBrand
                }
            }
        },
        methods: {
            tableRowClassName({row, rowIndex}) {
                if (rowIndex === 1) {
                    return 'warning-row';
                } else if (rowIndex === 3) {
                    return 'success-row';
                }
                return '';
            },
            //点击还原按钮后,将删除的商品恢复,并且重新渲染页面
            restoreBrand(brand){
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    axios({
                        method:"get",
                        url:"http://index.nat100.top/brand/updateRemoveBrand?id="+brand.id
                    }).then(()=>{
                        this.$message({
                            type: 'success',
                            message: '操作成功!'
                        });
                        //重新渲染页面
                        this.$store.dispatch('BrandAbout/removeBrand')
                })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消操作'
                    });          
                });                
            }
        }
    }
    export default EngineerLogTable
</script>