<!-- 日志 -->
<template>
    <div class="handoff" id="diary">
            <div class="handoffSearch">
                <EngineerSearch title="日志"></EngineerSearch>
                <input type="submit" value="返回" id="diary_back" v-on:click="back">
            </div>
            <EngineerLogTable></EngineerLogTable>
    </div>
</template>

<script>
    import EngineerSearch from '@/components/EngineerSearch.vue';
    import EngineerLogTable from '@/components/EngineerLogTable.vue';
    const EngineerLog = {
        name : 'EngineerLog',
        components :{
            EngineerSearch : EngineerSearch,
            EngineerLogTable : EngineerLogTable
        },
        methods: {
            //点击返回按钮,页面回到最初
            back(){
                this.$store.dispatch('BrandAbout/removeBrand')
                this.$store.state.BrandAbout.brand.brandName = ''
                this.$store.state.BrandAbout.brand.companyName = ''
            }
        },
        mounted() {
            //挂载完毕,清空来自其它页面的brand
            this.$store.state.BrandAbout.brand = {
                status: '',
                brandName: '',
                companyName: '',
                id: "",
                ordered: "",
                imageUrl:"无",
                flag: "1"
            }
            this.$store.dispatch('BrandAbout/removeBrand')
        },
    }
    export default EngineerLog
</script>

<style scoped>
    #diary_back {
        line-height: 1;
        white-space: nowrap;
        cursor: pointer;
        border: 1px solid #DCDFE6;
        color: #fbfbff;
        text-align: center;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        outline: 0;
        margin: 0;
        -webkit-transition: .1s;
        transition: .1s;
        font-weight: 500;
        padding: 12px 20px;
        font-size: 14px;
        border-radius: 4px;
        background-color: #727070;
    }
</style>