<template>
    <div class="box account" style="display: block;">
        <h1>注销账号</h1>
        <ul class="rightAccountFlex">
            <input type="text" placeholder="请输入账号" id="account_workNumber" v-model.lazy="workNumber">
            <input type="text" placeholder="请输入密码" id="account_password" v-model.lazy="password">
        </ul>
        <button class="account_user" v-on:click="destroyAccout">注销账号</button>
    </div>
</template>

<script>
    import axios from 'axios'
    const DestroyAccount = {
        name : 'DestoryAccount',
        data() {
            return {
                workNumber : '',
                password:''
            }
        },
        methods: {
            destroyAccout(){
                this.$confirm('此操作将永久删除账号内全部信息, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    if(this.workNumber === '' || this.password === ''){
                        this.$message({
                            message:'请完善账号信息',
                            type:'error'
                        })
                        return
                    }
                    if(this.workNumber === this.$store.state.UserAbout.username){//判断用户输入的账号是否属于自己
                        //2.检测输入的账号密码是否正确
                        axios({
                        method:"get",
                        url:"http://index.nat100.top/user/selectTbUser?username="+this.workNumber+"&password="+this.password
                        }).then(resp=>{
                        if(resp.data === 'success'){
                            //根据账号 删除用户信息
                            //删除已经完成支付的订单信息
                            axios({
                                method:"get",
                                url:"http://index.nat100.top/shopping/deleteFinishBuyServlet?shoppingAccount="+this.workNumber
                            }).then(()=>{
                                axios({
                                    method:"get",
                                    url:"http://index.nat100.top/userInformation/deleteUserInformation?account="+this.workNumber
                                }).then(()=>{
                                    //删除账号
                                    axios({
                                        method:"get",
                                        url:"http://index.nat100.top/user/deleteTbUser?username="+this.workNumber
                                    }).then(()=>{
                                            //删除用户存储的cookie
                                            this.$cookies.remove('carBrandInformations');
                                            location.href="http://localhost:8081";
                                    })
                                })
                            })
                        }
                        else{
                            this.$message({
                                message:'密码输入错误',
                                type:'error'
                            })
                        }
                        })                
                    }   
                    else{
                        this.$message({
                            message:'请检测账号输入是否正确',
                            type:'error'
                        })
                    }
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
                });
            }
        }
    }
    export default DestroyAccount
</script>

<style scoped>
    .account .rightAccountFlex {
        margin-top: 20px;
        margin-left: 90px;
        display: flex;
        flex-direction: column;
        position: relative;
        top: 130px;
        left: 180px;
    }
    .account .rightAccountFlex input {
        margin: 20px;
        width: 300px;
        height: 30px;
        color: #0FF;
        font-size: 20px;
        border: 0px transparent;
        border-bottom: 2px solid #fff;
        background-color: #171717;
    }
    .account .rightAccountFlex input {
        margin: 20px;
        width: 300px;
        height: 30px;
        color: #0FF;
        font-size: 20px;
        border: 0px transparent;
        border-bottom: 2px solid #fff;
        background-color: #171717;
    }
    .account .account_user {
        position: relative;
        top: 240px;
        margin-left: 255px;
        background: #fc5531;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        padding: 0 20px;
        text-align: center;
        width: 400px;
        height: 50px;
    }
</style>