<!-- 分页 -->
<template>
    <!-- total:总记录数 -->
    <!-- current-page :当前页码 -->
    <!-- page-size 每页显示条数 -->
    <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="$store.state.BrandAbout.currentPage"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="$store.state.BrandAbout.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="$store.state.BrandAbout.totalCount">
    </el-pagination>
</template>

<script>
    const EngineerPagination = {
        name : 'EngineerPagination',
        data() {
            return {
                // 每页显示的条数
                pageSize:5,
                // 总记录数
                totalCount:100,
                // 当前页码
                currentPage: 1,
            }
        },
        methods: {
            //分页
            handleSizeChange(val) {
                //console.log(`每页 ${val} 条`);
                // 重新设置每页显示的条数
                this.$store.dispatch('BrandAbout/newPageSize',val)
                this.$store.dispatch('BrandAbout/queryBrands')
                // this.selectAll();
            },
            handleCurrentChange(val) {
                //console.log(`当前页: ${val}`);
                // 重新设置当前页码
                this.$store.dispatch('BrandAbout/newCurrentPage',val)
                this.$store.dispatch('BrandAbout/queryBrands')
            }
        }
    }
    export default EngineerPagination
</script>

<style scoped>
    .el-pagination {
        position: fixed;
        bottom: 0;
    }
</style>