<template>
  <div class="shoppingBrand_root">
    <!-- 头部导航栏 -->
    <BuyBrandHeader titleNameLogo="购物车" titleNameHeader="个人中心"></BuyBrandHeader>
    <!-- 侧边栏 -->
    <ShoppingCarAside></ShoppingCarAside>
    <!-- 路由 -->
    <router-view></router-view>
    <!--遮盖-->
    <div id="hidebg" v-show="isHidebg"></div>
  </div>
</template>

<script>
    import BuyBrandHeader from '@/components/BuyBrandHeader.vue';
    import ShoppingCarAside from '@/components/ShoppingCarAside.vue';
    const ShoppingCar = {
        name : 'ShoppingCar',
        components:{
            BuyBrandHeader : BuyBrandHeader,
            ShoppingCarAside : ShoppingCarAside
        },
        data(){
          return {
            //判断遮罩是否显示
            isHidebg:false
          }
        },
        methods: {
          isShowHidebg(data){
            this.isHidebg = data
          }
        },
        mounted() {
          //绑定自定义事件,接收isChange,判断是否处于修改状态
          this.$bus.$on('isChangeHidebg',this.isShowHidebg)
        },
        beforeDestroy() {
          //解绑当前组件用到的自定义事件
          this.$bus.$off(['isChangeHidebg'])
        }
    }
    export default ShoppingCar
</script>

<style scoped>
  #hidebg {
      position:absolute;
      left:0px;
      top:0px;
      background-color:#000;
      width:100%;  /*宽度设置为100%，这样才能使隐藏背景层覆盖原页面*/
      height: 100%;
      filter:alpha(opacity=60);  /*设置透明度为60%*/
      opacity:0.6;  /*非IE浏览器下设置透明度为60%*/
      z-Index:8;
  }
  .shoppingBrand_root{
    height: 100vh;
    overflow: hidden;
    background-color: #fffdfd;
  }
</style>