<template>
    <div class="sideways">
        <ul class="sideways_flex">
            <li class="buy_car"  v-on:click="shoppingCarTab">
            购物车
            </li>
            <li class="finish_buy"  v-on:click="finishCarTab">
            已经支付
            </li>
        </ul>
    </div>
</template>

<script>
    const ShoppingCarAside = {
        name : 'ShoppingCarAside',
        methods:{
            //未支付订单
            shoppingCarTab(){
                this.$router.replace({
                    name : 'shoppingCar',
                    params:{
                        removeOne:false
                    }
                })
            },
            //已经完成支付的订单
            finishCarTab(){
                this.$router.replace({
                    name : 'finishCar',
                    params:{
                        removeOne:true
                    }
                })
            }
        }
    }
    export default ShoppingCarAside
</script>

<style scoped>
    .active{
        background-color:#fc5531;
    }
    .sideways {
        width: 200px;
        height: 91vh;
        background-color: #252525;
    }

    .sideways .sideways_flex {
        display: flex;
        flex-direction: column;
        text-align: center;
        font-size: 14px;
        color: aliceblue;
    }

    .sideways .sideways_flex .finish_buy {
        padding-top: 100px;
    }

    .sideways .sideways_flex .buy_car {
        padding-top: 100px;
    }

    .sideways .sideways_flex li {
        border-bottom: 1px solid #ccc;
    }

    .sideways .sideways_flex li:hover {
        cursor: pointer;
        background-color: #ff7050;
    }
</style>