<template>
    <div class="rightInformation" id="change_Information" style="display: none;">
        <h1>编辑信息</h1>
        <form id="form">
            <ul class="rightInformation_flex">
                <li class="one">
                    <span class="photo">头像:</span>
                    <input type="file" name="avatarPath" ref="testImageFile" class="fileHead"
                        accept="image/gif, image/jpeg, image/png, image/jpg" multiple="multiple">
                </li>
                <li class="two">
                    <span class="inforamtion_nickname">昵称:</span>
                    <input type="text" placeholder="请输入您的昵称" class="change_userInformation" id="information_name"
                        name="nickname" v-model="nickname">
                </li>
                <li class="three">
                    <span class="information_sex">性别:</span>
                    <select id="information_sexSelect" name="sex" v-model="sexvalue">
                        <option value="1" id="boy_information">男</option>
                        <option value="0" id="girl_information">女</option>
                    </select>
                </li>
                <li class="four">
                    <span class="information_account" id="accountMI">账号:</span>
                    <i class="flex_account" id="information_account">{{ username }}</i>
                    <input type="text" class="change_userInformation" id="account_none" name="account"
                        style="display: none">
                </li>
                <li class="five">
                    <span class="information_password">账号密码:</span>
                    <input type="text" placeholder="可修改密码" class="change_userInformation" id="information_password"
                        name="information_password" v-model="newPassword">
                </li>
                <li class="six">
                    <span class="information_city">国家/地区:</span>
                    <select id="information_citySelect" name="city" v-model="city">
                        <option value="中国" selected="selected">中国</option>
                        <option value="外国">外国</option>
                    </select>
                </li>
            </ul>
        </form>
        <button class="c_btn" id="change_confirm" v-on:click="changeUserInformation">确认</button>
        <button class="c_btn" id="change_back" v-on:click="informationShow">返回</button>
    </div>
</template>

<script>
import axios from 'axios';
import { mapState } from 'vuex';
const UserInformationArticleChange = {
    name: 'UserInformationArticleChange',
    data() {
        return {
            Nickname:'',
            Sexvalue:'',
            City:''
        }
    },
    computed: {
        ...mapState('UserAbout', ['username']),
        newPassword: {
            get() {
                return this.$store.state.UserAbout.password
            },
            set(value) {
                this.$confirm('此操作将使你退出登录状态, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    axios({
                        method:"get",
                        url:"http://index.nat100.top/user/updatePassword?account="+this.username+"&password="+value
                    }).then(()=>{
                        this.$message({
                            message:'修改成功',
                            type:'success'
                        })
                        location.href="http://localhost:8081"
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消操作'
                    })          
                })                
            }
        },
        nickname: {
            get() {
                return this.$store.state.UserAbout.nickname
            },
            set(value) {
                this.Nickname = value
            }
        },
        sexvalue: {
            get() {
                return this.$store.state.UserAbout.sexvalue
            },
            set(val) { 
                this.Sexvalue = val
            }
        },
        city: {
            get() {
                return this.$store.state.UserAbout.city
            },
            set(val) {
                this.City = val
            }
        }
    },
    methods: {
        informationShow() {
            this.$bus.$emit('isshow', true)
        },
        //上传修改后的个人信息
        changeUserInformation() {
            let forms = new FormData();
            //表单数据 name value 正确提交格式
            forms.append('account', this.username)
            forms.append('nickname', this.Nickname)
            forms.append('sex', this.Sexvalue)
            forms.append('city', this.City)
            let files = this.$refs.testImageFile.files
            //上传多个文件
            Array.from(files).forEach(item => {
                forms.append('file', item)
            })
            axios({
                method: "post", 
                url: "http://index.nat100.top/userInformation/uploadServlet",
                data: forms
            }).then((resp) => {
                if (resp.data === "success") {
                    this.$message({
                        message: '修改成功',
                        type: 'success'
                    })
                    this.$store.dispatch('UserAbout/querUserInformation')//重新查询数据
                }
            })
        }
    },
}
export default UserInformationArticleChange
</script>

<style scoped>
.rightInformation .rightInformation_flex {
    margin-top: 20px;
    margin-left: 90px;
}

.rightInformation .rightInformation_flex li {
    padding: 20px;
}

.rightInformation .rightInformation_flex li span {
    color: aliceblue;
    font-size: 20px;
}

.change_userInformation {
    margin: 20px;
    width: 300px;
    height: 30px;
    color: #0FF;
    font-size: 20px;
    border: 0px transparent;
    border-bottom: 2px solid #fff;
    background-color: black;
}

.rightInformation .rightInformation_flex li i {
    margin-left: 10px;
    color: #ccc;
    font-size: 16px;
}

.c_btn {
    margin-left: 117px;
    background: #fc5531;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    padding: 0 20px;
    text-align: center;
    width: 259px;
    height: 50px;
}</style>