<template>
    <div class="car_search">
        <input type="text" placeholder="商品名称" class="el-input__inner car_brandName" id="shoppingCar_inputBrandName"
            v-model.trim="brandName">
        <input type="text" placeholder="收件人姓名" class="el-input__inner car_addressee" id="shoppingCar_inputAddressee"
            v-model.trim="addressee">
        <button type="button" class="el-button el-button--primary" id="search_submit_btn" v-on:click="searchData">
            <span>查询</span>
        </button>
    </div>
</template>

<script>
const ShoppingCarSearch = {
    name: 'ShoppingCarSearch',
    props: ['title'],
    data() {
        return {
            brandName: '',
            addressee: ''
        }
    },
    methods: {
        //查询功能
        searchData() {
            if (this.title === 'shoppingCar') {//购物车中的查询功能
                if (this.brandName !== '' || this.addressee !== '') {
                    let brandData = []
                    let cookieData = this.$cookies.get('carBrandInformations')
                    for (let i = 0; i < cookieData.length; i++) {
                        if (cookieData[i].brandName.includes(this.brandName) && cookieData[i].addressee.includes(this.addressee)) {
                            brandData.push(cookieData[i])
                        }
                    }
                    //触发查询全局事件
                    this.$bus.$emit('searchTableData', brandData)
                }
                else {
                    this.$message({
                        message: '请完善查询信息',
                        type: 'warning'
                    })
                }
            }
            else {//已经支付订单的查询功能
                if(this.brandName === '' && this.addressee === ''){
                    this.$message({
                        message:'请完善信息',
                        type:'warning'
                    })
                    return
                }
                let brandArr = [this.brandName,this.addressee]
                this.$store.dispatch('ShoppingCarAbout/searchFinishBrandCondition',brandArr)
            }

        }
    }
}
export default ShoppingCarSearch
</script>

<style scoped>
.car_search {
    display: flex;
    position: relative;
    top: -100px;
}

.el-input__inner {
    margin: 0;
    width: 205px;
    font-family: Microsoft YaHei, Heiti SC, tahoma, arial, Hiragino Sans GB, "\5B8B\4F53", sans-serif;
    font-size: inherit;
    background-color: #FFF;
    background-image: none;
    border-radius: 4px;
    border: 1px solid #DCDFE6;
    box-sizing: border-box;
    color: #606266;
    display: inline-block;
    height: 40px;
    line-height: 40px;
    outline: 0;
    padding: 0 15px;
    transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
    cursor: pointer;
    padding-right: 30px;
}

.car_search .el-button {
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    margin: 0;
    transition: .1s;
    font-weight: 500;
    padding: 12px 20px;
    font-size: 14px;
    border-radius: 4px;
    color: #FFF;
    background-color: #fc5531;
    border-color: #fc5531;
}

.car_search input {
    margin-right: 10px;
}</style>