<template>
    <div class="handoff" id="brandImages">
        <div class="brand_image">
            <span>查看商品图片</span>
            <EngineerSearch title="商品图片"></EngineerSearch>
            <div class="brand_image_flex">
                <img :src="'../assets/images/'+imageUrl" alt="请修改/输入你的查询信息" id="brand_showImage" v-if="imageUrl !== 'image'">
                <span class="brand_image_name" v-show="brandName !== ''">商品姓名:</span> <span class="image_name">{{ brandName }}</span>
            </div>
        </div>
    </div>
</template>

<script>
    import EngineerSearch from '@/components/EngineerSearch.vue';
    const EngineerLookBrand = {
        name : 'EngineerLookBrand',
        components :{
            EngineerSearch : EngineerSearch
        },
        computed:{
            brandName:{
                get(){
                    return this.$store.state.BrandAbout.brand.brandName
                }
            },
            imageUrl:{
                get(){
                    if(this.$store.state.BrandAbout.brand.imageUrl === '无'){
                        return 'image'
                    }
                    return this.$store.state.BrandAbout.brand.imageUrl
                }
            }
        }
    }
    export default EngineerLookBrand
</script>

