<template>
    <div class="finishCarRoot">
        <el-table
        @selection-change="handleSelectionChange"
        :data="$store.state.ShoppingCarAbout.finishBuyBrandInformation"
        style="width: 1230px">
        <el-table-column
        type="selection"
        width="55"
        max-height="675">
        </el-table-column>
        <el-table-column
        fixed
        prop="brandName"
        label="商品名称"
        align='center'
        width="150">
        </el-table-column>
        <el-table-column
        prop="brandImg"
        label="商品"
        align='center'
        width="200">
            <template slot-scope="scope">
                <el-image 
                    :src="require('../assets/images/'+scope.row.brandImg)"
                    style="width: 100px; height: 100px"
                    lazy 
                ></el-image>
            </template>
        </el-table-column>
        <el-table-column
        prop="receiptAddress"
        label="收件地址"
        align='center'
        width="300">
        </el-table-column>
        <el-table-column
        prop="brandNumber"
        label="购买数量"
        align='center'
        width="120">
        </el-table-column>
        <el-table-column
        prop="price"
        label="价格"
        align='center'
        width="120">
        </el-table-column>
        <el-table-column
        prop="addressee"
        label="收件人"
        align='center'
        width="120">
        </el-table-column>
        <el-table-column
        fixed="right"
        label="操作"
        width="120">
            <template slot-scope="scope">
                <el-button
                @click.native.prevent="deleteRow(scope.$index, $store.state.ShoppingCarAbout.finishBuyBrandInformation)"
                type="text"
                size="small">
                移除
                </el-button>
            </template>
        </el-table-column>
    </el-table>
    </div>
</template>

<script>
    import axios from 'axios';
    const FinishCarTable = {
        name : 'FinishCarTable',
        methods: {
            //移除
            deleteRow(index, rows) {
                let id = rows[index].id//需要被移除的商品的id
                this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    //发送请求 开始删除(单个删除)
                    axios({
                        method:"get",
                        url:"http://index.nat100.top/shopping/deleteFinishBrandServlet?id="+id
                    }).then(()=>{
                        this.$message({
                            message:'删除成功',
                            type:'success'
                        })
                        //重新查询数据
                        this.$store.dispatch('ShoppingCarAbout/searchFinishBrand')
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    })          
                })
            },
            handleSelectionChange(val) {
                this.$bus.$emit('selectFinishCars',val)
            }
        }
    }
    export default FinishCarTable
</script>

<style scoped>
    .finishCarRoot{
        position: absolute;
        top: 20px;
    }
</style>