<!-- 修改商品的弹出框 -->
<template>
    <el-dialog
                    title="修改品牌"
                    :visible.sync="dialogVisible"
                    width="30%"
            >

                <el-form ref="form" :model="$store.state.BrandAbout.brand" label-width="80px">
                    <el-form-item label="品牌名称">
                        <el-input v-model="$store.state.BrandAbout.brand.brandName"></el-input>
                    </el-form-item>

                    <el-form-item label="企业名称">
                        <el-input v-model="$store.state.BrandAbout.brand.companyName"></el-input>
                    </el-form-item>

                    <el-form-item label="价格">
                        <el-input v-model="$store.state.BrandAbout.brand.ordered"></el-input>
                    </el-form-item>

                    <el-form-item label="图片路径">
                        <el-input type="textarea" v-model="$store.state.BrandAbout.brand.imageUrl"></el-input>
                    </el-form-item>

                    <el-form-item label="状态">
                        <el-switch v-model="$store.state.BrandAbout.brand.status"
                                   active-value="1"
                                   inactive-value="0"
                        ></el-switch>
                    </el-form-item>


                    <el-form-item>
                        <template>
                            <el-button type="primary" v-on:click="changeBrand">提交</el-button>
                            <el-button v-on:click="dialogVisible=!dialogVisible">取消</el-button>
                        </template>
                    </el-form-item>
                </el-form>

            </el-dialog>
</template>

<script>
    import axios from 'axios';
    const ChangeBrandDialog = {
        name : 'ChangeBrandDialog',
        data() {
            return {
                //修改的弹框
                dialogVisible:false
            }
        },
        methods: {
            //修改数据
            //确认修改之后 , 修改商品信息
            changeBrand(){
                let newBrandName = this.$store.state.BrandAbout.brand.brandName;
                if(this.$store.state.BrandAbout.brand.brandName === "" || this.$store.state.BrandAbout.brand.companyName === "" || this.$store.state.BrandAbout.brand.imageUrl === "" || this.$store.state.BrandAbout.brand.ordered === "" || this.$store.state.BrandAbout.brand.imageUrl === ""){
                    this.$message({
                        showClose: true,
                        message: '请完善商品信息',
                        type: 'error'
                    })
                }
                else{
                    //先判断商品图片是否有重复
                    axios({
                        method:"get",
                        url:"http://index.nat100.top/brand/selectImageUrl?imageUrl="+this.$store.state.BrandAbout.brand.imageUrl+"&id="+this.$store.state.BrandAbout.brand.id
                    }).then((resp) => {
                        if (resp.data === "exist") {
                            this.$message({
                                showClose: true,
                                message: '图片重复',
                                type: 'error'
                            })
                        }
                        else{
                            //判断新修改的商品是否会产生重复
                            axios({
                                method:"get",
                                url:"http://index.nat100.top/brand/selectRepeat?brandName="+this.$store.state.BrandAbout.brand.brandName+"&companyName="+this.$store.state.BrandAbout.brand.companyName+"&id="+this.$store.state.BrandAbout.brand.id
                            }).then((resp) => {
                                console.log('查询情况:'+resp.data);
                                if(resp.data === 0){
                                    this.$message({
                                        showClose: true,
                                        message: '该商品已被删除,请查看日志',
                                        type: 'error'
                                    })
                                }
                                else if (resp.data === 1){
                                    this.$message({
                                        showClose: true,
                                        message: '请勿重复添加商品',
                                        type: 'error'
                                    })
                                }
                                else if(resp.data === "no_repeat"){
                                    console.log('商品模型',this.$store.state.BrandAbout.brand);
                                    //修改商品信息
                                    axios({
                                        method:"post",
                                        url:"http://index.nat100.top/brand/updateBrand?id="+this.$store.state.BrandAbout.brand.id,
                                        data:JSON.stringify(this.$store.state.BrandAbout.brand)
                                    }).then((resp) => {
                                        if(resp.data === "success"){
                                            axios({
                                                method:"get",
                                                url:"http://index.nat100.top/brand/updateImageName?id="+this.$store.state.BrandAbout.brand.id+"&newBrandName="+newBrandName+"&imageUrl="+this.$store.state.BrandAbout.brand.imageUrl
                                            }).then((resp) => {
                                                if(resp.data === "success"){
                                                    this.$message({
                                                        showClose: true,
                                                        message: '修改成功',
                                                        type: 'success'
                                                    });
                                                    //清空brand
                                                    this.$store.state.BrandAbout.brand = {
                                                        status: '',
                                                        brandName: '',
                                                        companyName: '',
                                                        id: "",
                                                        ordered: "",
                                                        imageUrl:"无",
                                                        flag: "1"
                                                    }
                                                    //关闭窗口
                                                    this.dialogVisible = false;
                                                    // 重新查询数据
                                                    this.$store.dispatch('BrandAbout/queryBrands')
                                                }
                                            })
                                        }
                                    })
                                }
                            })
                        }
                    })
                }
            },
        },
        mounted() {
            this.$bus.$on('isDialogVisible',(value)=>{
                this.dialogVisible = value
            })
        },
        beforeDestroy() {
            this.$bus.$off(['isDialogVisible'])
        },
    }
    export default ChangeBrandDialog
</script>