<template>
    <!-- 购买商品 -->
    <div class="viewFlex">
        <div class="left">
            <img class="brandImg" :src="require('../assets/images/'+queryBrand.imageUrl)" alt="" id="brand">
        </div>
        <div class="right brandInformation">
            <ul class="viewInformation">
                <i class="viewOne">
                    <span class="viewName">{{ queryBrand.brandName }}</span>
                    <span class="viewCompanyName">{{ queryBrand.companyName }}</span>
                </i>
                <i class="viewMoney">
                    {{ queryBrand.ordered }} 元
                </i>
                <i class="viewStatus" v-show="queryBrand.status == 0" style="color: black;">
                    {{ queryBrand.statusStr }}
                </i>
                <i class="viewBtn">
                    <button id="buyBrand" v-bind:class="{active : queryBrand.status == 0}" v-on:click="userBuy">确认购买</button>
                    <button id="buyCar" v-bind:class="{active : queryBrand.status == 0}" v-on:click="userAddCar">添加购物车</button>
                    <button id="topBrand" v-on:click="back">取消购买</button>
                </i>
            </ul>
        </div>
        <BuyBrandAddress v-bind:isShow="isShow" v-bind:isAddress="isAddress" v-bind:titleNameBtn="titleNameBtn"></BuyBrandAddress>
    </div>
</template>

<script>   
    import { mapState } from 'vuex';
    import BuyBrandAddress from './BuyBrandAddress.vue';
    const BuyBrandArticle = {
        name: 'BuyBrandArticle',
        data() {
            return {
                isShow:false,
                isAddress:false,//是否给用户提供添加默认地址的选项
                titleNameBtn:''
            }
        },
        components:{
            BuyBrandAddress : BuyBrandAddress
        },
        computed:{
            ...mapState('BrandAbout',['queryBrand'])
        },
        methods: {
            //确认购买
            userBuy(){
                if(this.queryBrand.status == 0){
                    this.$message({
                        message:'货存不足',
                        type:'warning'
                    })
                }
                else{
                    this.isShow = true
                    this.isAddress = true
                    this.titleNameBtn = '支付'
                }
            },
            //添加购物车
            userAddCar(){
                if(this.queryBrand.status == 0){
                    this.$message({
                        message:'货存不足',
                        type:'warning'
                    })
                }
                else{
                    this.isShow = true
                    this.isAddress = false
                    this.titleNameBtn = '添加'
                }
            },
            //取消购买
            back(){
                this.$router.push({
                    name : 'index'
                })
            }
        },
        mounted() {
            this.$bus.$on('closeShow',(value)=>{
                this.isShow = value
                this.isAddress = value
            })
        },
        beforeDestroy() {
            this.$bus.$off(['closeShow'])
        }
    }
    export default BuyBrandArticle
</script>

<style scoped>
    .active{
        background-color: #eeeeee !important;
        color: #ffffff;
    }
    .viewFlex{
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        background-color: #ccc;
        width: 990px;
        /* height: 800px; */
        margin: 0 auto;
        display: flex;
        position: relative;
        top: 220px;
    }
    .viewFlex .left{
    /* margin-top: 200px; */
    background-color:#ffffff;
    height: 400px;
    width: 400px;
    }

    #brand{
        width: 408px;
    }

    .viewFlex .right .viewInformation{
        display: flex;
        flex-direction: column;
    }

    .viewFlex .right .viewInformation i{
        padding: 20px;
        font-size: 16px;
        font-weight: 700;
        color: coral;
    }

    .viewFlex .right .viewInformation .viewOne .viewName{
        padding-right: 10px;
    }

    #buyBrand{
        position: absolute;
        top: 250px;
        left: 412px;
        background: #fc5531;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        padding: 0 20px;
        text-align: center;
        width: 200px;
        height: 50px;
    }

    #topBrand{
        position: absolute;
        top: 250px;
        left: 685px;
        background: #fc5531;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        padding: 0 20px;
        text-align: center;
        width: 200px;
        height: 50px;
    }

    #buyCar{
        position: absolute;
        top: 80px;
        left: 685px;
        background: #fc5531;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        padding: 0 20px;
        text-align: center;
        width: 200px;
        height: 50px;
    }
</style>