<template>
    <div class="left">
        <ul class="left_flex">
            <li class="btn" v-on:click="brandChange(0)" v-bind:class="{active : changedActive == 0}">商品修改</li>
            <li class="btn" v-on:click="engineerInformation(1)" v-bind:class="{active : changedActive == 1}">个人中心</li>
            <li class="btn" v-on:click="engineerLog(2)" v-bind:class="{active : changedActive == 2}">日志</li>
            <li class="btn" v-on:click="lookBrand(3)" v-bind:class="{active : changedActive == 3}">查看商品</li>
        </ul>
    </div>
</template>

<script>
    const EngineerBrandAside = {
        name : 'EngineerBrandAside',
        data() {
            return {
                changedActive:window.sessionStorage.getItem('changedActive') === null ? 0 : window.sessionStorage.getItem('changedActive')
            }
        },
        watch:{
            //监视changedActive,changedActive的值一旦改变,将其存到本地,保证刷新不改变页面
            changedActive:{
                immediate:true,
                deep:false,
                handler(newValue,oldValue){
                    window.sessionStorage.setItem('changedActive',newValue) 
                }
            }
        },
        methods: {
            //商品修改
            brandChange(index){
                this.changedActive = index
                this.$router.replace({
                    name : 'engineerChange'
                })
            },
            //个人中心
            engineerInformation(index){
                this.changedActive = index
                this.$router.replace({
                    name : 'engineerInformation'
                })
            },
            //日志
            engineerLog(index){
                this.changedActive = index
                this.$router.replace({
                    name : 'engineerLog'
                })
            },
            //查看商品
            lookBrand(index){
                this.changedActive = index
                this.$router.replace({
                    name : 'engineerLookBrand'
                })
            }
        }
    }
    export default EngineerBrandAside
</script>

<style scoped>
    .left {
        width: 150px;
        position: absolute;
        background-color: rgba(7, 120, 189, 0.83);
    }
    .left .left_flex {
        display: flex;
        flex-direction: column;
    }
    .left .left_flex li:nth-child(-n+3) {
        border-bottom: 1px solid #cccccc;
    }
    .left .left_flex li {
        width: 150px;
        /* height: 200px; */
        line-height: 199px;
        text-align: center;
        color: white;
        font-size: 14px;
    }
    .active{
        background-color: #409eff;
    }
    .btn:hover{
        background-color: #409eff;
    }
</style>