<!-- 分页-->
<template>
    <div>
        <div id="isNav">
            <button disabled="false" id="prev">上一页</button>
            <button id="next">下一页</button>
        </div>
        <p id="pagination">共?页，当前?页</p>
    </div>
</template>

<script>
    const ShoppingCarPagination = {
        name : 'ShoppingCarPagination'
    }
    export default ShoppingCarPagination
</script>

<style>
    #isNav{
        position: fixed;
        bottom: 0;
    }
    #pagination{
        position: fixed;
        bottom: 0;
        left: 395px;
    }
    #prev,#next{
        line-height: 1;
        white-space: nowrap;
        cursor: pointer;
        margin: 0;
        transition: .1s;
        font-weight: 500;
        padding: 12px 20px;
        font-size: 14px;
        border-radius: 4px;
        color: #FFF;
        background-color: #fc5531;
        border-color: #fc5531;
    }
</style>