<template>
    <div>
        <EngineerAccountComponent 
            title="工程师登录" 
            in3="邀请码(必填)" 
            btn="Login">
        </EngineerAccountComponent>
        <span class="forgetPassword" v-on:click="forgetPassword">忘记密码?</span>
    </div>
</template>

<script>
    //引入页面组件
    import EngineerAccountComponent from '@/components/EngineerAccountComponent';
    const EngineerLogin = {
        name : 'EngineerLogin',
        components :{
            EngineerAccountComponent : EngineerAccountComponent
        },
        methods:{
            //点击忘记密码,
            forgetPassword(){
                this.$router.push({
                    name : 'engineerPassword'
                })
            }
        }
    }
    export default EngineerLogin
</script>


<style scoped>
    .forgetPassword{
        width: 100px;
        height: 20px;
        font-size: 14px;
        color: black;
        position: absolute;
        right: 205px;
        bottom: 90px;
    }

    .forgetPassword:hover{
        cursor: pointer;
    }

</style>