<template>
    <div>
        <UserInformationArticleShow v-show="isShow"></UserInformationArticleShow>
        <UserInformationArticleChange v-show="!isShow"></UserInformationArticleChange>
    </div>
</template>

<script>
    import UserInformationArticleShow from '@/components/UserInformationArticleShow.vue';
    import UserInformationArticleChange from '@/components/UserInformationArticleChange.vue';
    const UserInformationShow = {
        name : 'UserInformationShow',
        components : {
            UserInformationArticleShow : UserInformationArticleShow,
            UserInformationArticleChange : UserInformationArticleChange
        },
        data() {
            return {
                isShow:true
            }
        },
        methods: {
            isShowInformation(value){
                this.isShow = value
            }
        },
        mounted() {
            this.$bus.$on('isshow',this.isShowInformation);
        },
        beforeDestroy() {
            this.$bus.$off(['isshow'])
        }
    }
    export default UserInformationShow
</script>
