<template>
    <div class="search_function">
        <button type="button" class="el-button el-button--primary" id="search_remove_btn" v-on:click="deleteBrands">
            <span>批量删除</span>
        </button>
        <button type="button" class="el-button el-button--primary" id="search_back_btn"
            style="background-color:#cccccc; margin-left: 20px;" v-on:click="back">
            <span>返回</span>
        </button>
    </div>
</template>

<script>
import axios from 'axios';
const ShoppingCarFunction = {
    name: 'ShoppingCarFunction',
    props: ['title'],
    data() {
        return {
            brandCars: [],
            finishCars:[]
        }
    },
    methods: {
        deleteBrands() {//批量删除功能
            if (this.title === 'shoppingCar') {//购物车的批量删除
                let buyBrandInformation = this.$cookies.get('carBrandInformations')
                let removeBrands = this.brandCars//需要删除的元素
                let removeIds = []
                for(let i = 0 ; i < removeBrands.length ; i++){
                    removeIds.push(removeBrands[i].id)
                }
                buyBrandInformation = buyBrandInformation.filter(item => !removeIds.includes(item.id))//批量删除操作
                //先删除cookie
                this.$cookies.remove('carBrandInformations')
                //创建新的cookie
                this.$cookies.set('carBrandInformations', buyBrandInformation)
                //触发全局事件总线,重新渲染页面
                this.$bus.$emit('changeCookie')
            }
            else {//已经支付订单的批量删除
                let ids = []
                for(let i = 0 ; i < this.finishCars.length ; i++){
                    ids.push(this.finishCars[i].id)
                }
                if(ids.length === 0){
                    this.$message({
                        message:'请勾选你想要删除的商品',
                        type:'warning'
                    })
                }
                else{
                    axios({
                        method:"post",
                        url:"http://index.nat100.top/shopping/deleteFinishBrandByIdsServlet",
                        data:JSON.stringify(ids)
                    }).then(()=>{
                        this.$message({
                            message:'操作成功',
                            type:'success'
                        })
                        //重新查询数据
                        this.$store.dispatch('ShoppingCarAbout/searchFinishBrand')
                    })
                }
            }
        },
        //返回
        back() {
            if (this.title === 'shoppingCar') {
                //触发全局事件,使其重新查询一次原cookie
                this.$bus.$emit('changeCookie')
            }
            else{
                //重新查询一次数据,渲染也买你
                this.$store.dispatch('ShoppingCarAbout/searchFinishBrand')
            }
        }
    },
    mounted() {
        //通过全局事件总线获取复选框被选中的购物车商品的id
        this.$bus.$on('selectShoppingCars', (data) => {
            this.brandCars = data
        })
        //通过全局事件总线获取复选框被选中的已经支付的订单的id
        this.$bus.$on('selectFinishCars',(data)=>{
            this.finishCars = data
        })
    },
    beforeDestroy() {
        this.$bus.$off(['selectShoppingCars','selectFinishCars'])
    },
}
export default ShoppingCarFunction
</script>

<style scoped>
.search_function {
    position: relative;
    top: -80px;
}

.search_function .el-button {
    -webkit-font-smoothing: antialiased;
    font-family: Microsoft YaHei, Heiti SC, tahoma, arial, Hiragino Sans GB, "\5B8B\4F53", sans-serif;
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    box-sizing: border-box;
    outline: 0;
    margin: 0;
    transition: .1s;
    font-weight: 500;
    padding: 12px 20px;
    font-size: 14px;
    border-radius: 4px;
    color: #FFF;
    background-color: #ee3f4d;
    border-color: #ee3f4d;
}
</style>