<template>
    <!--搜索表单-->
    <el-form :inline="true" :model="$store.state.BrandAbout.brand" class="demo-form-inline">
            <el-form-item label="当前状态" v-show="title==='修改'">
                <el-select v-model="$store.state.BrandAbout.brand.status" placeholder="当前状态">
                    <el-option label="启用" value="1"></el-option>
                    <el-option label="禁用" value="0"></el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="企业名称">
                <el-input v-model="$store.state.BrandAbout.brand.companyName" placeholder="企业名称"></el-input>
            </el-form-item>

            <el-form-item label="品牌名称">
                <el-input v-model="$store.state.BrandAbout.brand.brandName" placeholder="品牌名称"></el-input>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" id="submit_btn" v-on:click="onSubmit">查询</el-button>
            </el-form-item>
    </el-form>
</template>

<script>
import axios from 'axios';

    const EngineerSearch = {
        name : 'EngineerSearch',
        props:['title'],
        methods: {
            onSubmit(){
                //如果title是修改,则查询未被删除的商品的信息 如果是日志,则查询被删除的商品 如果是商品图片,则查看商品的详细信息
                if(this.title === '修改'){
                    this.$store.dispatch('BrandAbout/queryBrands')
                } 
                else if(this.title === '日志'){
                    let brand = {
                        brandName:this.$store.state.BrandAbout.brand.brandName,
                        companyName:this.$store.state.BrandAbout.brand.companyName
                    }
                    this.$store.dispatch('BrandAbout/searchRemoveBrand',brand)
                }
                else{
                    if(this.$store.state.BrandAbout.brand.brandName === '' || this.$store.state.BrandAbout.brand.companyName === ''){
                        this.$message({
                            message: '请完善查询信息',
                            type: 'error'
                        })
                    }
                    else{
                        axios({
                            method : 'get',
                            url : 'http://index.nat100.top/brand/selectConfirmBrandServlet?brandName='+this.$store.state.BrandAbout.brand.brandName+'&companyName='+this.$store.state.BrandAbout.brand.companyName
                        }).then(resp=>{
                            if(resp.data !== null){
                                this.$store.state.BrandAbout.brand.brandName = resp.data.brandName
                                this.$store.state.BrandAbout.brand.imageUrl = resp.data.imageUrl
                            }
                            else{
                                this.$message({
                                    message: '查询结果为空',
                                    type: 'error'
                                });
                            }
                        })
                    }
                }
            }
        },
    }
    export default EngineerSearch
</script>