<template>
    <!-- 导航栏 -->
    <nav class="clearfix">
        <div class="nav w">
                <ul class="nav-flex">
                    <li>
                        <img src="../assets/images/logo-mi2.png" alt="" class="mi-logo">
                    </li>
                    <li v-for="(title,index) in titles" v-bind:key="index">
                        <a href="javascript:;" class="ff6700">{{title}}</a>
                    </li>
                    <li>
                        <input type="text" class="search show" placeholder="小米" id="top_search" v-model="searchBrandName">
                    </li>
                </ul>
        </div>
             <!--搜索商品的信息提示-->
        <div class="search_tip" v-show="userSearchByBrandNames.length !== 0" id="searchTip">
            <ul class="tip_information">
                <li class="tip_brandName" v-for="(userSearchByBrandName,index) in userSearchByBrandNames" :key="index">
                    <!-- http://localhost:8081/Java_war/brandBuy.html?id=${id} -->
                    <a href="javascript:;" v-on:click="buyBrand(userSearchByBrandName.id)" class="show">{{ userSearchByBrandName.brandName }}  {{ userSearchByBrandName.companyName }}</a>
                </li>
            </ul>
        </div>
    </nav>
</template>

<script>
    import axios from 'axios';
    const IndexNav = {
        name : 'IndexNav',
        data(){
            return {
                titles:['Xiaomi手机','Redmi手机','电视','笔记本','平板','家电','路由器','服务中心','社区'],
                searchBrandName:'',
                userSearchByBrandNames:[]
            }
        },
        watch:{
            searchBrandName:{
                immediate:false,
                deep:false,
                handler(newValue,oldValue){
                    this.userSearchByBrandNames = []//在每一次重新查询之后,清空上一次的查询结果
                    axios({
                        method:"get",
                        url:"http://index.nat100.top/brand/selectBrandBySearchServlet?brandName="+newValue
                    }).then(resp=>{
                        let formData = resp.data
                        for(let i = 0 ; i < formData.length ; i++){
                            this.userSearchByBrandNames.push(formData[i])
                        }
                    })
                }
            }
        },
        methods: {
            //获取商品id跳转到购物界面
            buyBrand(id){
                this.$router.push({
                    name :'buyBrand'
                })
                //将商品对应的id存入会话
                sessionStorage.setItem('BrandId',id)
            }
        },
        mounted() {
            //页面监听 点击除搜索框以及提示框以为的区域,盒子隐藏
            document.onclick = ($event)=>{
                let e = $event
                if(!e.target.classList.contains('show')){
                    this.userSearchByBrandNames=[]
                }
            }
        },
    }
    export default IndexNav
</script>

<style scoped>
   .ff6700:hover {
        color: #ff6700;
    }
    nav {
        width: 100%;
        height: 100px;
        position: relative;
    }

    .mi-logo {
        height: 56px;
        width: 56px;
        display: block;
        position: absolute;
        top: 20px;
        left: 116px;
    }

    /* 导航栏中间部分 */
    nav .nav .nav-flex {
        display: flex;
        line-height: 100px;
        margin-left: 142px;
    }

    nav .nav .nav-flex li {
        font-size: 13px;
        font-weight: 700;
        margin: 0 20px;
    }

    /* 搜索框 */
    nav .nav .nav-flex li .search {
        border: 1px solid #7d7d7d;
        width: 235px;
        height: 50px;
        padding-left: 10px;
    }
    .search_tip{
        position: absolute;
        top: 85px;
        left: 975px;
        width: 360px;
        height: 195px;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
        background-color: #fff;
        z-index: 3;
        border-radius: 20px;
        overflow: scroll;
        -ms-overflow-style: none;
        overflow-x: hidden;
        overflow-y: auto;
    }

    .search_tip .tip_information{
        text-align: center;
        display: flex;
        flex-direction: column;
    }

    .search_tip .tip_information li{
        font-size: 14px;
        color: black;
        line-height: 40px;
        height: 40px;
    }

    .search_tip .tip_information li:first-child{
        margin-top: 20px;
    }

    .search_tip .tip_information li:hover{
        cursor: pointer;
        background-color: #cccccc;
    }

    .search_tip .tip_information .tip_brandName a{
        padding-right: 90px;
        padding-left: 90px;
        padding-top: 20px;
        padding-bottom: 20px;
    }

</style>