import cookies from "vue-cookies";
import axios from "axios";
const shoppignCar = {
    namespaced:true,
    //actions--用于响应组件中的动作(业务逻辑,发送请求写在actions里)
    actions:{
        isShowChange(context,value){
            context.commit('ISSHOWCHANGE',value)
        },
        //查询用户已经购买的商品
        searchFinishBrand(context){
            axios({
                method:"get",
                url:"http://index.nat100.top/shopping/selectFinishMoneyServlet?shoppingAccount="+localStorage.getItem('username')
            }).then(resp=>{
                context.commit('SEARCHFINISHBRAND',resp.data)
            })
        },
        //根据条件查询用户想要搜索的商品
        searchFinishBrandCondition(context,brandArr){
            axios({
                method:"post",
                url:"http://index.nat100.top/shopping/selectFinishLikeServlet?brandName="+brandArr[0]+"&addressee="+brandArr[1]+"&account="+localStorage.getItem('username'),
            }).then((resp)=>{
                context.commit('SEARCHFINISHBRAND',resp.data)
            })
        }
    },
    //准备mutations 用于操作数据
    mutations:{
        ISSHOWCHANGE(state,value){
            state.isShow = value
        },
        //查询用户已经购买的商品
        SEARCHFINISHBRAND(state,value){
            state.finishBuyBrandInformation = value
        },
    },
    //准备state-用于存储数据
    state:{
        isShow:false,
        //用户已经支付的商品的信息
        finishBuyBrandInformation:[]
    },
    //准备getters - 用于加工state中的数据
    getters:{

    }
}

export default shoppignCar
